// Tables
$ln-table-cell-label-width: 100px !default;
$ln-table-collapse-map: (
  md: map-get($ln-breakpoints, 'md')
) !default;

// Icons
$ln-icon-sizes: (
  small: 0.6em,
  def: 1.2em,
  large: 1.5em
) !default;

// @deprecated
$ln-padding-x-input: $ln-spacing-unit !default;
$ln-padding-y-input-and-button: ($ln-spacing-unit * 0.75) !default;
