@function ln-map-set($map, $key, $value) {
  $new: ($key: $value);
  @return map-merge($map, $new);
}

@function ln-closest-breakpoint-value($target-breakpoint, $spacing-percentages) {
  $result: map-get($spacing-percentages, $target-breakpoint);
  $position: index(map-keys($ln-breakpoints), $target-breakpoint);

  @while $result == null {
    $bp: 'def';

    @if $position > 0 {
      $bp: nth(nth($ln-breakpoints, $position), 1);
    }

    $result: map-get($spacing-percentages, $bp);
    $position: $position - 1;
  }

  @return $result;
}

/* Font size */

@function ln-px-to-rem-font-size($target-px, $breakpoint) {
  $scale: map-get($ln-spacing-percentages, $breakpoint);

  @if $scale == null {
    $scale: ln-closest-breakpoint-value($breakpoint, $ln-spacing-percentages);
  }

  $v: $target-px / (($scale / 100%) * $ln-browser-font-size-default-px);

  @return $v * 1rem;
}

@function ln-px-to-rem-font-sizes($font-sizes-pixels) {
  $result: ();
  @each $type-key, $type-map in $font-sizes-pixels {
    $type-result: ();
    @each $breakpoint, $target-px in $type-map {
      $type-result: ln-map-set(
        $type-result,
        $breakpoint,
        ln-px-to-rem-font-size($target-px, $breakpoint)
      );
    }

    @each $breakpoint, $percentage in $ln-spacing-percentages {
      $font-size: map-get($type-map, $breakpoint);

      @if $font-size == null {
        $font-size: ln-closest-breakpoint-value($breakpoint, $type-map);
        $type-result: ln-map-set(
          $type-result,
          $breakpoint,
          ln-px-to-rem-font-size($font-size, $breakpoint)
        );
      }
    }

    $ordered-type-result: ('def': map-get($type-result, 'def'));
    @each $breakpoint in map-keys($ln-breakpoints) {
      @if map-has-key($type-result, $breakpoint) {
        $ordered-type-result: ln-map-set($ordered-type-result, $breakpoint, map-get($type-result, $breakpoint));
      }
    }

    $result: ln-map-set($result, $type-key, $ordered-type-result);
  }

  @return $result;
}

/* Line height */

@function ln-line-height-to-px-size($line-height, $font-size) {
  @return $font-size * $line-height;
}

@function ln-line-height-to-px-sizes($line-height, $font-sizes) {
  $v: ();

  @each $breakpoint, $font-size in $font-sizes {
    $v: ln-map-set($v, $breakpoint, ln-line-height-to-px-size($line-height, $font-size));
  }

  @each $breakpoint, $percentage in $ln-spacing-percentages {
    $font-size: map-get($v, $breakpoint);

    @if $font-size == null {
      $font-size: ln-closest-breakpoint-value($breakpoint, $font-sizes);

      $v: ln-map-set($v, $breakpoint, ln-line-height-to-px-size($line-height, $font-size));
    }
  }

  @return $v;
}
