$font-display: fallback;

// Texta regular
@font-face {
  font-display: $font-display;
  font-family: Texta;
  font-style: normal;
  font-weight: 400;
  src: url('#{$tu-assets-url}fonts/Texta-Regular.woff2') format('woff2'),
    url('#{$tu-assets-url}fonts/Texta-Regular.woff') format('woff');
}

// Texta medium
@font-face {
  font-display: $font-display;
  font-family: Texta;
  font-style: normal;
  font-weight: 700;
  src: url('#{$tu-assets-url}fonts/Texta-Bold.woff2') format('woff2'),
    url('#{$tu-assets-url}fonts/Texta-Bold.woff') format('woff');
}

// Texta bold
@font-face {
  font-display: $font-display;
  font-family: Texta;
  font-style: normal;
  font-weight: 900;
  src: url('#{$tu-assets-url}fonts/Texta-Black.woff') format('woff');
}
