
@if $use-ln-password {
  /// @deprecated in favor of .ln-c-input-group
  .ln-c-password {
    position: relative;
  }

  .ln-c-password__input {
    @include ln-text-input;
    padding-right: 5rem;
  }

  .ln-c-password__toggle-button {
    @include ln-font-size('body');
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: $ln-spacing-unit;
    color: $ln-color-black;
    font-weight: bold;
    text-transform: uppercase;
  }
}
