$icon-size: (
  // 10px
    small: 0.6em,
  // 20px
    medium: 1em,
  // 36px
    large: 2em,
  // 64px
    xlarge: 3em,
  // 64px
    xxlarge: 4em
);
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  svg:not(:root) {
    overflow: visible !important;
  }
}

.ln-c-icon--large {
  width: map-get($icon-size, large);
  height: map-get($icon-size, large);
}

.ln-c-icon--xlarge {
  cursor: pointer;
  width: map-get($icon-size, xlarge);
  height: map-get($icon-size, xlarge);
}

.ln-c-icon--standout {
  width: auto;
  height: map-get($icon-size, large);
  max-width: 100%;

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    width: map-get($icon-size, xxlarge);
  }
}

.ln-c-icon--hero {
  width: auto;
  height: map-get($icon-size, xxlarge);

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    width: map-get($icon-size, xxlarge);
  }
}

.ln-c-icon--payment {
  width: 2.5em;
  height: 1.875em;
}

.ln-c-icon--cart {
  width: 1.5em;
  height: 1.5em;
}

.ln-c-icon--disabled {
  opacity: 0.5;
}

.ln-c-icon--confirmation {
  vertical-align: top;
  width: map-get($icon-size, medium);
  height: map-get($icon-size, medium);
  margin-right: $ln-spacing-unit-half;
}

.tu-u-icon--mobile-filter {
  transform: rotate(0.5turn);
  left: -3em;
  position: absolute;
  box-shadow: 4px -4px 10px 0 rgba(50, 50, 50, 0.5);

  &:disabled {
    opacity: 0;
    cursor: none;

    @include ln-respond-to('xs') {
      opacity: 0.2;
      cursor: not-allowed;
    }
  }

  @include ln-respond-to('xs') {
    position: static;
    left: auto;
    box-shadow: none;
  }

  &::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    background-color: $tu-color-quill-gray;
    right: 100%;
    top: 0;
    z-index: 2;

    @include ln-respond-to('xs') {
      height: 70%;
      top: auto;
    }
  }
}
