.tu-c-overlay {
  @include ln-full-screen;
  right: -100%;
  background-color: $ln-color-grey-v-light;
  opacity: 0;
  transition: opacity 0.5s;

  &.active {
    outline: none;
    right: 0;
    opacity: 0.5;
  }
}

.has-overlay {
  position: relative;
  overflow: hidden;

  /* Remove ui-mobile style once jquery-mobile is removed */
  &.ui-mobile {
    height: 100% !important;
  }
}
