.tu-u-responsive-padding {
  padding-top: 0;

  @include ln-respond-to('sm') {
    padding-top: $ln-spacing-unit-x2;
  }

  &--left {
    padding-left: $ln-spacing-unit;
    padding-top: $ln-spacing-unit-x2;

    @include ln-respond-to('sm') {
      padding-left: $ln-spacing-unit-x2;
    }
  }

  &--right {
    padding-right: 0;
    padding-left: $ln-spacing-unit;

    @include ln-respond-to('sm') {
      padding-right: $ln-spacing-unit-x2;
      padding-left: 0;
    }
  }

  &--full {
    padding: $ln-spacing-unit;

    @include ln-respond-to('sm') {
      padding: $ln-spacing-unit-x2;
    }
  }

  /* take out once old styles are deleted */
  .content {
    padding: 0;
  }
}
