////
/// Colours
/// @group settings/colours/base
////

/// Orange
/// @type Color
$ln-color-orange: #f06c00 !default;

/// Orange dark
/// @type Color
$ln-color-orange-dark: #e55000 !default;

/// Plum
/// @type Color
$ln-color-plum: #7f0442 !default;

/// White
/// @type Color
$ln-color-white: #fff !default;

/// Grey very light
/// @type Color
$ln-color-grey-v-light: #f6f6f6 !default;

/// Grey light
/// @type Color
$ln-color-grey-light: #d7d7d7 !default;

/// Grey
/// @type Color
$ln-color-grey: #8e8e8e !default;

/// Grey dark
/// @type Color
$ln-color-grey-dark: #4c4c4c !default;

/// Black
/// @type Color
$ln-color-black: #0c0c0c !default;

/// Red
/// @type Color
$ln-color-red: #db3e34 !default;

/// Aqua
/// @type Color
$ln-color-aqua: #00a9cf !default;

/// Green
/// @type Color
$ln-color-green: #2aa742 !default;

/// Yellow
/// @type Color
$ln-color-yellow: #ffc911 !default;

/// Transparent
/// @type Color
$ln-color-transparent: transparent !default;

$ln-colors: (
  'orange': $ln-color-orange,
  'orange-dark': $ln-color-orange-dark,
  'plum': $ln-color-plum,
  'white': $ln-color-white,
  'grey-v-light': $ln-color-grey-v-light,
  'grey-light': $ln-color-grey-light,
  'grey': $ln-color-grey,
  'grey-dark': $ln-color-grey-dark,
  'black': $ln-color-black,
  'red': $ln-color-red,
  'aqua': $ln-color-aqua,
  'green': $ln-color-green,
  'yellow': $ln-color-yellow
);

// Typography

/// Base text colour
/// @group settings/colours/typography
/// @type Color
$ln-color-base-text: $ln-color-grey-dark !default;

/// Base link colour
/// @group settings/colours/typography
/// @type Color
$ln-color-links: currentColor !default;

// General

/// Site background colour
/// @group settings/colours/general
/// @type Color
$ln-color-site-background: $ln-color-white !default;

/// Standard border colour
/// @group settings/colours/general
/// @type Color
$ln-color-base-border: $ln-color-grey !default;

/// Error colour
/// @group settings/colours/general
/// @type Color
$ln-color-error: $ln-color-red;

/// Warning colour
/// @group settings/colours/general
/// @type Color
$ln-color-warning: $ln-color-yellow;

$ln-color-highlight-background: rgba($ln-color-black, 0.06) !default;

// Input fields
$ln-color-input-background: $ln-color-white !default;
$ln-color-background-disabled: $ln-color-grey-v-light !default;
$ln-color-input-border: $ln-color-grey !default;
$ln-color-input-border-focus: $ln-color-grey-dark !default;

// Password field strength meter colour
$ln-color-password-meter-default: $ln-color-grey-light !default;

// Buttons
$ln-color-button-primary: $ln-color-white !default;
$ln-color-button-primary-background: $ln-color-orange !default;
$ln-color-button-primary-background-hover: $ln-color-orange-dark !default;

$ln-color-button-secondary: $ln-color-white !default;
$ln-color-button-secondary-background: $ln-color-grey !default;
$ln-color-button-secondary-hover: $ln-color-white !default;
$ln-color-button-secondary-background-hover: $ln-color-grey-dark !default;

$ln-color-button-disabled: $ln-color-grey !default;
$ln-color-button-disabled-background: $ln-color-grey-light !default;

// Badges
$ln-color-badge-primary: $ln-color-orange !default;
$ln-color-badge-secondary: $ln-color-grey-dark !default;

//  Deprecated colours
$ln-color-orange-light: #f90 !default;
$ln-color-plum-light: #a50052 !default;
$ln-color-plum-dark: #57002b !default;
$ln-color-grey-v-dark: #242424 !default;
$ln-color-blue: #427fed !default;

$ln-color-alpha-light: $ln-color-orange-light !default;
$ln-color-alpha: $ln-color-orange !default;
$ln-color-alpha-dark: $ln-color-orange-dark !default;

$ln-color-beta-light: $ln-color-plum-light !default;
$ln-color-beta: $ln-color-plum !default;
$ln-color-beta-dark: $ln-color-plum-dark !default;

$ln-color-gamma: $ln-color-red !default;
$ln-color-delta: $ln-color-aqua !default;
$ln-color-epsilon: $ln-color-green !default;
$ln-color-zeta: $ln-color-yellow !default;
$ln-color-eta: $ln-color-blue !default;
