.tu-c-pager {
  a {
    display: inherit;
    padding: 0 0.25rem;
    font-size: 1rem;
    line-height: 2rem;
    text-decoration: none !important;
    text-align: center;
    width: 2rem;

    &:hover {
      text-decoration: underline !important;
    }
  }

  .tu-c-pager--current {
    background-color: $ln-color-white;
    border-radius: 2rem;
  }
}

.tu-c-pager--prev {
  transform: rotate(180deg);
}
