@keyframes showImage {
  from {
    height: 0;
  }

  to {
    height: 100%;
  }
}

.tu-lightbox__container {
  position: fixed;
  background: rgba($ln-color-white, 0.8);
  width: 100%;
  height: 0;
  display: block;
  overflow-x: hidden;
  z-index: 10000;

  &--show {
    animation-delay: 0.25s;
    animation-duration: 1.25s;
    animation-name: showImage;
    animation-fill-mode: forwards;
  }

  &--paypal {
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.tu-lightbox__click {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.tu-lightbox__content {
  position: relative;
  max-width: map-get($ln-breakpoints, 'md') - 100;
  margin: 0 auto;
  padding: $ln-spacing-unit-double 0;

  .tu-lightbox__container--paypal & {
    margin: 0;
  }
}

.tu-lightbox__image,
.tu-lightbox__html {
  margin: 0 auto;
  padding: 0 $ln-spacing-unit-half;
  position: relative;

  @include ln-respond-to('sm') {
    margin: 0 2.5rem;
  }

  .content {
    background-color: $ln-color-white;
    padding: 0;
    margin: 0;
  }

  img {
    max-width: none;
    position: relative;
  }

  @include ln-respond-to('max-sm') {
    .ln-c-tabs {
      padding-right: $ln-spacing-unit-x3;
    }
  }
}

.tu-lightbox__image-drag {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
}

.tu-lightbox__image-drag-area {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: url('data:image/png;base64,'),
    auto;

  &--disabled {
    cursor: not-allowed;
    background-color: $ln-color-white;
  }
}

.tu-lightbox__btn {
  background-color: $tu-color-cape-cod;
  border: 0;
  width: 2em;
  height: 2em;
  position: absolute;
  right: $ln-spacing-unit-half;
  padding: 0;

  @include ln-respond-to('sm') {
    right: -1.75rem;
  }

  .ln-c-icon {
    vertical-align: inherit;
  }
}

.tu-lightbox__container--open {
  overflow: hidden;
  position: fixed;
}
