.has-error .ln-c-input-container {
  position: relative;

  @include ln-respond-to('xs') {
    max-width: $tu-form-maxwidth;
  }

  &::after {
    content: '!';
    color: $ln-color-white;
    background: $ln-color-error;
    min-width: $ln-size-def;
    min-height: $ln-size-def;
    line-height: $ln-size-def;
    font-size: 20px;
    font-weight: 700;
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    z-index: 2;
  }
}

.ln-c-password {
  width: 100%;

  @include ln-respond-to('xs') {
    max-width: $tu-form-maxwidth;
  }
}

.ln-c-text-input,
.ln-c-password__input {
  background: $tu-input-background none;
  border-color: $tu-color-black;
  font-family: inherit;
  border-width: 0 0 1px;
  max-width: auto;

  @include ln-respond-to('xs') {
    max-width: $tu-form-maxwidth;
  }

  &:not([disabled]) {
    &:hover {
      box-shadow: none;
    }

    &:focus {
      box-shadow: 0 0 10px 2px rgba(162, 199, 230, 0.5);
      background-color: $ln-color-white;
      border-color: $tu-input-interaction;
    }

    .ln-c-form-group.has-error & {
      background-image: none;
      border-bottom-width: 2px;
      border-bottom-color: $ln-color-error;
    }
  }

  &--textarea {
    background-color: $tu-input-background;
    font-family: $ln-font-family-brand;
    padding-left: $ln-spacing-unit;
    padding-right: $ln-spacing-unit;
  }
}

.ln-c-label {
  .paymentFormPostcodeLookup & {
    display: block;
    margin-top: 2rem;
    margin-bottom: -0.5rem;
    margin-left: 0.5rem;

    @include ln-respond-to('xs') {
      margin-bottom: 0.5rem;
    }
  }
}
