/* Slider */

.slick-carousel-container {
  visibility: hidden;
  overflow: hidden;
  max-height: 480px;

  &.slick-initialized {
    visibility: visible;
    overflow: auto;
    max-height: inherit;
  }
}

.slick-slider {
  position: relative;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;

  .slick-carousel-container & {
    display: block;
  }
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  display: flex;

  &.slick-product-description {
    display: block;
  }

  &::before,
  &::after {
    content: '';
    display: table;
  }

  &::after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  min-height: 1px;

  > div {
    display: flex;
    height: 100%;
    width: 100%;
  }

  [dir='rtl'] & {
    float: right;
  }

  .tile {
    height: 100%;
  }

  img {
    display: block;
  }

  &.slick-loading img,
  &.slick-loading .tu-c-spinner {
    display: none;
  }

  &.dragging img {
    pointer-events: none;
  }

  .slick-product-description.slick-initialized & {
    display: block;
  }

  .slick-carousel-container.slick-initialized & {
    display: flex;
  }

  .slick-initialized & {
    display: flex;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: flex;
    height: auto;
    border: 1px solid transparent;
  }
}

.slick-arrow.slick-hidden {
  display: none;
}

.tu-c-product-card__navigation {
  .slick-dots {
    padding-left: $ln-spacing-unit-half;
    padding-right: $ln-spacing-unit-half;
    margin-bottom: 0;

    li {
      display: inline-block;
    }

    button {
      cursor: pointer;
      width: 8px;
      height: 8px;
      line-height: 8px;
      background-color: $tu-color-boulder;
      overflow: hidden;
      text-indent: 200px;
      border-radius: 50%;
      margin: 0 5px;
    }

    .slick-active button {
      background-color: $tu-color-black;
    }
  }
}

.tu-homepage__carousel-dots {
  .slick-dots {
    margin: 2rem auto;
    width: 50%;
    text-align: center;

    li {
      display: inline-block;
    }

    button {
      width: 3rem;
      height: 8px;
      line-height: 8px;
      background-color: $tu-color-boulder;
      overflow: hidden;
      text-indent: 200px;
      margin: 0 5px;
    }

    .slick-active button {
      background-color: $tu-color-cape-cod;
    }
  }
}

.slick-product-description__thumbnails {
  .slick-dots {
    text-align: center;

    @include ln-respond-to('max-sm') {
      margin-top: 10px;
    }
  }

  li {
    display: inline-block;
    margin-bottom: $ln-spacing-unit-half;
    width: auto;
    padding: 0 $ln-spacing-unit 0 0;

    @include ln-respond-to('sm') {
      display: block;
      padding: 0;
      width: 100%;
    }
  }

  button {
    background: none;
    background-color: transparent;
    background-image: none;
    border: 0;
    cursor: pointer;
    width: 100%;
    height: auto;
    line-height: auto;
    position: relative;
    padding: 0;

    @include ln-respond-to('max-sm') {
      width: 15px;
      height: 15px;
      border-radius: 50%;
    }
  }

  button::after {
    content: '';
    top: 0;
    left: 0;
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    border: 2px solid $tu-color-cape-cod;
    display: none;

    @include ln-respond-to('max-sm') {
      display: block;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 1px solid $tu-color-cape-cod;
    }
  }

  .slick-active button::after {
    display: block;

    @include ln-respond-to('max-sm') {
      background-color: $tu-color-cape-cod;
    }
  }

  @include ln-respond-to('max-sm') {
    img {
      display: none;
    }
  }
}

.slick-arrow {
  background-color: $ln-color-white;
  background-image: none;
  cursor: pointer;
  width: 2em;
  height: 2em;
  color: $tu-color-cape-cod;

  &.slick-disabled {
    cursor: not-allowed;
  }

  /** Avoid removing styles from legacy carousel **/
  .js-product-carousel &,
  .js-product-description-carousel &,
  .js-homepage-carousel & {
    &.slick-disabled {
      opacity: 0.5;
    }
  }

  svg {
    width: 100%;
    height: 100%;

    .tu-homepage__carousel & {
      width: 50%;
      height: 50%;
    }
  }
}

.slick-product-description__navigation-container {
  position: absolute;
  top: 50%;
  height: 0;
  z-index: 2;
  width: 100%;
}

.slick-product-description__navigation {
  height: 0;
  width: 100%;
  position: relative;
  margin-top: -1.5rem;

  .slick-arrow {
    border: 0;
    background-color: $ln-color-white;
    width: 3em;
    height: 3em;

    &.slick-disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  .slick-next {
    margin-right: $ln-spacing-unit;
    float: right;
  }
}

// homepage carousel -- desktop
.tu-homepage__carousel {
  overflow: hidden;
  display: block;
  padding: 0;

  .slick-track {
    display: block;
  }

  .slick-carousel-container.slick-initialized .slick-slide {
    background-color: $ln-color-white;
    display: inline-block;
    margin-right: $ln-spacing-unit-x2;
  }

  .slick-arrow {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 6rem;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
  }

  &:hover {
    .slick-arrow {
      opacity: 1;
    }
  }

  .slick-next {
    left: auto;
    right: 0;
  }

  .slick-list.draggable {
    margin-left: -8rem;
  }

  img,
  .text-overlay {
    position: relative;
    display: block;
    padding-bottom: $ln-spacing-unit;
    width: 100%;
  }

  h3 {
    padding-left: 25%;
  }

  @include ln-respond-to('xs') {
    padding: $ln-spacing-unit-x2 0 $ln-spacing-unit-x2 $ln-spacing-unit-x2;

    .slick-list.draggable {
      margin-left: 0;
    }

    h3 {
      padding-left: $ln-spacing-unit;
    }
  }
}
