@mixin ln-spacing {
  @each $key, $value in $ln-spacing-percentages {
    @if $key == 'def' {
      font-size: $value;
    }

    @else {
      @if map-has-key($ln-breakpoints, $key) {
        /* stylelint-disable-next-line max-nesting-depth */
        @include ln-respond-to($key) {
          font-size: $value;
        }
      }

      @else {
        @warn 'No "#{$key}" breakpoint found';
      }
    }
  }
}
