// Qualifying type is used to prevent misuse
// Utility only intended for use on anchor tags within CMS content blocks
// stylelint-disable selector-no-qualifying-type
a.tu-u-background-link {
  // Overly specific selector, only usable in specific CMS component
  // https://jira.sainsburysargos.io/browse/TUC-4324
  .tu-c-homepage__textcontent--image-background
    > .tu-c-homepage__text
    > &:first-child {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
