@if $use-ln-border {
  .ln-u-border--double {
    border: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-top {
  .ln-u-border-top--double {
    border-top: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-right {
  .ln-u-border-right--double {
    border-right: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-bottom {
  .ln-u-border-bottom--double {
    border-bottom: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-left {
  .ln-u-border-left--double {
    border-left: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-sides {
  .ln-u-border-sides--double {
    border-left: 2px solid $tu-color-cape-cod !important;
    border-right: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border-ends {
  .ln-u-border-ends--double {
    border-bottom: 2px solid $tu-color-cape-cod !important;
    border-top: 2px solid $tu-color-cape-cod !important;
  }
}

@if $use-ln-border {
  .ln-u-border--lightgray {
    border-color: $tu-color-wild-sand !important;
  }

  .ln-u-border--gray {
    border-color: $tu-color-quill-gray !important;
  }

  .ln-u-border--white {
    border-color: $ln-color-white !important;
  }

  .ln-u-border--yellow {
    border-color: $tu-color-tangerine-yellow !important;
  }
}
