$tu-box-shadow: 0 0 15px 5px rgba(50, 50, 50, 0.2);

.tu-c-filters {
  background-color: $ln-color-white;
  position: fixed;
  top: 0;
  width: 86%;
  height: 100%;
  overflow-x: visible;
  z-index: 9998;

  @include ln-respond-to('xs') {
    min-width: $tu-form-maxwidth;
    width: 50%;
  }

  &--sortby {
    left: -100%;
    transition: left 0.5s ease;

    &.is-open {
      left: 0;
      box-shadow: $tu-box-shadow;
    }
  }

  &--filterby {
    right: -100%;
    transition: right 0.5s ease;

    &.is-open {
      right: 0;
      box-shadow: $tu-box-shadow;
    }
  }

  &.is-open {
    outline: none;

    .tu-filters {
      position: relative;
      z-index: 1000;
      right: 1rem;
    }
  }
}

.tu-c-filter__count {
  text-align: center;

  @include ln-respond-to('sm') {
    text-align: right;
  }
}

.tu-c-sort-filters {
  position: relative;
  margin-bottom: $ln-spacing-unit-x2;
}

.tu-c-filters__title {
  padding-left: $ln-spacing-unit-x2;

  @include ln-respond-to('xs') {
    padding-left: 0;
  }
}

.tu-c-filters__facet-value {
  position: absolute;
  right: -100%;
  width: 100%;
  top: 3em;
  bottom: 0;
  overflow-y: scroll;
  overflow-x: visible;
  transition: right 0.75s ease;
  z-index: 1001;

  .nav-open & {
    right: 0;
    background-color: $ln-color-white;
  }
}

.tu-c-filters__facet-title {
  border-bottom: 1px solid $tu-color-quill-gray;
  cursor: pointer;

  @include ln-respond-to('xs') {
    font-size: 1rem;
  }
}

.tu-c-filters__facet-item {
  padding-left: $ln-spacing-unit-x2;
  padding-right: $ln-spacing-unit-x2;

  @include ln-respond-to('xs') {
    padding-left: $ln-spacing-unit-x3;
    padding-right: $ln-spacing-unit-x3;
  }

  &--sticky {
    position: sticky;
    bottom: 0;
  }

  .ln-c-form-option__input:checked ~ .tu-c-filters__facet-label {
    position: relative;
    font-weight: $ln-font-weight-medium;

    &::after {
      content: '';
      @include ln-tick(0.5rem, $ln-color-black, auto, 1rem);
      right: 0.75rem;
    }
  }
}

.tu-c-filters__facet-label {
  border-bottom: 1px solid $tu-color-quill-gray;
}

.tu-c-filter__item[data-position='secondary'] {
  display: none;

  [data-expansion='open-all'] & {
    display: list-item;
  }
}
