%bread-crumb {
  @each $key, $value in map-get($ln-font-sizes, $ln-font-size-base) {
    @if $key == 'xs' {
      font-size: $value;
    }
  }
}

.ln-c-breadcrumbs {
  display: block;
  max-width: $tu-max-width;
  min-width: $tu-wrapper-width;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: $ln-spacing-unit;
}

.ln-c-breadcrumbs__item {
  position: relative;

  &::after {
    @extend %bread-crumb;
    content: '/';
    line-height: 1em;
    position: absolute;
    top: 0;
    right: -0.8rem;
  }

  &--last {
    &::after {
      display: none;
    }

    a {
      text-decoration: underline;
    }
  }
}
