.tu-c-fit-as-expected {
  &__tu-main {
    display: inline-flex;
    align-items: center;
    justify-content: normal;
  }

  &__tu-u-progress-bar {
    height: 10.5px;
    max-width: $tu-form-maxwidth;
    width: 100%;
    margin: 5px;
    flex-grow: 1;
  }

  &__tu-u-tool-tip-header-wrapper {
    flex-grow: 1;
    width: auto;
  }

  &__tu-u-close-tool-tip {
    flex-grow: 0.1;
  }

  &__tu-popup-close {
    display: none;
  }

  &__tu-icon-svg-wrapper {
    background-color: $ln-color-white;
    color: $ln-color-black;
    display: inline-flex;
    align-items: center;
    min-height: 50px;
    border-radius: 25px;
    padding-right: 0.5rem;

    > a {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }

  &__tu-icon-lozenge-svg {
    margin-right: 0.5rem;
    min-width: 50px;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    color: $ln-color-white;
    background-color: $tu-color-black;
    text-align: center;
    padding: 8px;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      padding: 2px 18px 5px 6px;
      overflow: visible;
    }
  }

  &__tu-u-show {
    position: absolute;
    padding: 5px;
    background: $ln-color-white none repeat scroll 0 0;
    box-shadow: $tu-color-black 0 0 8px;
    transition: opacity 0.3s ease-in-out 0s, visibility 0.3s ease-in-out 0s;
    opacity: 1;
    visibility: visible;
    z-index: 50;
  }

  &__tu-u-progress-bar-wrapper {
    text-align: center;
  }

  &__tu-u-progress-bar-graph {
    width: auto;
    min-width: 80px;
  }

  &__tu-u-filler {
    height: 100%;
    width: 100%;
  }

  &__tu-u-popup-wrapper {
    position: absolute;
    left: -420px;
    background: $ln-color-white none repeat scroll 0 0;
    margin: 0 auto;
    z-index: 2;
    box-shadow: 0 0 7px $tu-color-wild-sand;
  }

  &__tu-u-popup-inner {
    min-width: 420px;
    width: 100%;
    max-width: 450px;
    padding: 20px 0 0 35px;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 100%;
      border: 15px solid transparent;
      border-top-color: $ln-color-white;
      transform: rotate(270deg);
    }
  }

  @include ln-respond-to('xs') {
    &__tu-u-progress-bar {
      width: 100%;
      max-width: 200px;
    }
  }

  @include ln-respond-to('max-sm') {
    &__tu-popup-close {
      display: block;
      position: relative;
      top: 37.75%;
      z-index: 1;
    }

    &__tu-u-popup-inner {
      margin: 0 auto;
      background: $ln-color-white;
      padding: 2rem 2rem 3rem;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
    }

    &__tu-u-popup-inner::after {
      display: none;
    }

    &__tu-u-progress-bar {
      width: 100%;
      max-width: 240px;
    }

    &__tu-u-popup-wrapper {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      padding: 0;
    }

    &__inner_wrapper {
      background: rgba(0, 0, 0, 0.5);
      height: 100vh;
      padding: 0;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}

.tc-c-fit-as-expected__tu-icon-close-pop-up {
  width: map-get($icon-size, large);
  height: map-get($icon-size, large);

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    width: map-get($icon-size, large);
    height: map-get($icon-size, small);
    overflow: visible !important;
  }
}
