$button-width: 300px;

.ln-c-button {
  height: auto;
  letter-spacing: 1px;
  text-align: center;
  width: 100%;

  @include ln-respond-to('xs') {
    max-width: $button-width;

    &--full {
      width: 100%;
      max-width: none;
      max-width: 100%;
    }

    &--inline {
      width: auto;
      padding: 0;
    }
  }

  &--primary,
  &--secondary,
  &--primary-alt {
    text-transform: uppercase;
  }

  &--primary,
  &--primary-alt {
    border: 0;
  }

  &--primary-alt {
    color: $tu-color-black;
    background-color: $ln-color-white;

    &:hover,
    &:focus {
      background-color: $ln-color-button-primary-background-hover;
      color: $ln-color-button-primary;
    }
  }

  &--secondary {
    background-color: transparent;
    border: 2px solid $ln-color-black;
    color: $tu-color-black;

    &:hover,
    &:focus {
      background-color: $ln-color-white;
      color: $ln-color-button-secondary-hover;
      border-color: $ln-color-button-secondary-hover;
    }
  }

  &--loading {
    background-color: transparent !important;
    background-image: url('/_ui/shared/images/loader.gif');
    background-position: 50%;
    background-repeat: no-repeat;
    text-indent: -500px;
  }

  &--lozenge {
    background: $tu-color-wild-sand;
    border-radius: 2rem;
    letter-spacing: 0;
    padding: 0.5rem 1rem;
    text-decoration: none;
    font-weight: 400;

    @include ln-respond-to('xs') {
      padding: 0.3rem 0.5rem;
    }

    &:hover,
    &:focus {
      background-color: $ln-color-button-primary-background-hover;
      color: $ln-color-button-primary;
    }
  }

  &--delete {
    &::after {
      content: 'x';
      font-weight: 700;
      padding-left: 0.5rem;
    }
  }

  &--text-only {
    font-weight: $ln-font-weight-regular;
    text-transform: lowercase;
    text-decoration: underline;
    border-width: 0;

    &:hover,
    &:focus {
      color: $ln-color-button-primary-background-hover;
    }
  }

  &.address-lookup {
    padding: 0.5rem;
  }

  &[type='submit'] {
    display: inline-block;
  }

  &[disabled] {
    border: none;
  }
}
