$tu-checkout-height: 60px;

.ln-c-tabs__list-wrap {
  .ln-c-tabs--checkout & {
    height: $tu-checkout-height;

    &::after {
      top: 0;
    }
  }
}

.ln-c-tabs__link {
  @include ln-font-size('h5');

  &:hover,
  &:focus,
  &.is-active {
    color: $tu-color-cape-cod;
  }

  &.is-active::before {
    background-color: $tu-color-cape-cod;
  }

  .ln-c-tabs--checkout & {
    @include ln-font-size('h6');
    color: $ln-color-black;
    font-weight: $ln-font-weight-medium;
    line-height: $tu-checkout-height;
    text-transform: uppercase;

    &:not(:last-child) {
      border-right: 0;
    }

    &.is-active::before {
      width: 100%;
      height: 100%;
      background-color: transparent;
      border: 2px solid $ln-color-black;
    }
  }
}
