////
/// @group utilities/margins
////

@if $use-ln-push {
/* stylelint-disable */
/// Adds `$ln-spacing-unit` margin to the specified side, with multiples of `$ln-spacing-unit` available
/// @name Push
/// @example markup - Default
///   <div class="ln-u-push">ln-u-push</div>
///   <div class="ln-u-push-top">ln-u-push-top</div>
///   <div class="ln-u-push-right">ln-u-push-right</div>
///   <div class="ln-u-push-bottom">ln-u-push-bottom</div>
///   <div class="ln-u-push-left">ln-u-push-bottom</div>
///   <div class="ln-u-push-sides">ln-u-push-sides</div>
///   <div class="ln-u-push-ends">ln-u-push-ends</div>
/// @example markup - 1 / 4 x spacing unit
///   <div class="ln-u-push*1/4">ln-u-push*1/4</div>
///   <div class="ln-u-push-top*1/4">ln-u-push-top*1/4</div>
///   <div class="ln-u-push-right*1/4">ln-u-push-right*1/4</div>
///   <div class="ln-u-push-bottom*1/4">ln-u-push-bottom*1/4</div>
///   <div class="ln-u-push-left*1/4">ln-u-push-bottom*1/4</div>
///   <div class="ln-u-push-sides*1/4">ln-u-push-sides*1/4</div>
///   <div class="ln-u-push-ends*1/4">ln-u-push-ends*1/4</div>
/// @example markup - 1 / 2 x spacing unit
///   <div class="ln-u-push*1/2">ln-u-push*1/2</div>
///   <div class="ln-u-push-top*1/2">ln-u-push-top*1/2</div>
///   <div class="ln-u-push-right*1/2">ln-u-push-right*1/2</div>
///   <div class="ln-u-push-bottom*1/2">ln-u-push-bottom*1/2</div>
///   <div class="ln-u-push-left*1/2">ln-u-push-bottom*1/2</div>
///   <div class="ln-u-push-sides*1/2">ln-u-push-sides*1/2</div>
///   <div class="ln-u-push-ends*1/2">ln-u-push-ends*1/2</div>
/// @example markup - 2 x spacing unit
///   <div class="ln-u-push*2">ln-u-push*2</div>
///   <div class="ln-u-push-top*2">ln-u-push-top*2</div>
///   <div class="ln-u-push-right*2">ln-u-push-right*2</div>
///   <div class="ln-u-push-bottom*2">ln-u-push-bottom*2</div>
///   <div class="ln-u-push-left*2">ln-u-push-bottom*2</div>
///   <div class="ln-u-push-sides*2">ln-u-push-sides*2</div>
///   <div class="ln-u-push-ends*2">ln-u-push-ends*2</div>
/// @example markup - 3 x spacing unit
///   <div class="ln-u-push*3">ln-u-push*3</div>
///   <div class="ln-u-push-top*3">ln-u-push-top*3</div>
///   <div class="ln-u-push-right*3">ln-u-push-right*3</div>
///   <div class="ln-u-push-bottom*3">ln-u-push-bottom*3</div>
///   <div class="ln-u-push-left*3">ln-u-push-bottom*3</div>
///   <div class="ln-u-push-sides*3">ln-u-push-sides*3</div>
///   <div class="ln-u-push-ends*3">ln-u-push-ends*3</div>
/// @example markup - 4 x spacing unit
///   <div class="ln-u-push*4">ln-u-push*4</div>
///   <div class="ln-u-push-top*4">ln-u-push-top*4</div>
///   <div class="ln-u-push-right*4">ln-u-push-right*4</div>
///   <div class="ln-u-push-bottom*4">ln-u-push-bottom*4</div>
///   <div class="ln-u-push-left*4">ln-u-push-bottom*4</div>
///   <div class="ln-u-push-sides*4">ln-u-push-sides*4</div>
///   <div class="ln-u-push-ends*4">ln-u-push-ends*4</div>
/* stylelint-enable */
  .ln-u-push { margin: $ln-spacing-unit !important; }
  .ln-u-push\*1\/4 { margin: ($ln-spacing-unit / 4) !important; }
  .ln-u-push\*1\/2 { margin: ($ln-spacing-unit / 2) !important; }
  .ln-u-push\*2 { margin: ($ln-spacing-unit * 2) !important; }
  .ln-u-push\*3 { margin: ($ln-spacing-unit * 3) !important; }
  .ln-u-push\*4 { margin: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-push-top {
  .ln-u-push-top { margin-top: $ln-spacing-unit !important; }
  .ln-u-push-top\*1\/4 { margin-top: ($ln-spacing-unit / 4) !important; }
  .ln-u-push-top\*1\/2 { margin-top: ($ln-spacing-unit / 2) !important; }
  .ln-u-push-top\*2 { margin-top: ($ln-spacing-unit * 2) !important; }
  .ln-u-push-top\*3 { margin-top: ($ln-spacing-unit * 3) !important; }
  .ln-u-push-top\*4 { margin-top: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-push-right {
  .ln-u-push-right { margin-right: $ln-spacing-unit !important; }
  .ln-u-push-right\*1\/4 { margin-right: ($ln-spacing-unit / 4) !important; }
  .ln-u-push-right\*1\/2 { margin-right: ($ln-spacing-unit / 2) !important; }
  .ln-u-push-right\*2 { margin-right: ($ln-spacing-unit * 2) !important; }
  .ln-u-push-right\*3 { margin-right: ($ln-spacing-unit * 3) !important; }
  .ln-u-push-right\*4 { margin-right: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-push-bottom {
  .ln-u-push-bottom { margin-bottom: $ln-spacing-unit !important; }
  .ln-u-push-bottom\*1\/4 { margin-bottom: ($ln-spacing-unit / 4) !important; }
  .ln-u-push-bottom\*1\/2 { margin-bottom: ($ln-spacing-unit / 2) !important; }
  .ln-u-push-bottom\*2 { margin-bottom: ($ln-spacing-unit * 2) !important; }
  .ln-u-push-bottom\*3 { margin-bottom: ($ln-spacing-unit * 3) !important; }
  .ln-u-push-bottom\*4 { margin-bottom: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-push-left {
  .ln-u-push-left { margin-left: $ln-spacing-unit !important; }
  .ln-u-push-left\*1\/4 { margin-left: ($ln-spacing-unit / 4) !important; }
  .ln-u-push-left\*1\/2 { margin-left: ($ln-spacing-unit / 2) !important; }
  .ln-u-push-left\*2 { margin-left: ($ln-spacing-unit * 2) !important; }
  .ln-u-push-left\*3 { margin-left: ($ln-spacing-unit * 3) !important; }
  .ln-u-push-left\*4 { margin-left: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-push-sides {
  .ln-u-push-sides {
    margin-right: $ln-spacing-unit !important;
    margin-left: $ln-spacing-unit !important;
  }

  .ln-u-push-sides\*1\/4 {
    margin-right: ($ln-spacing-unit / 4) !important;
    margin-bottom: ($ln-spacing-unit / 4) !important;
  }

  .ln-u-push-sides\*1\/2 {
    margin-right: ($ln-spacing-unit / 2) !important;
    margin-bottom: ($ln-spacing-unit / 2) !important;
  }

  .ln-u-push-sides\*2 {
    margin-right: ($ln-spacing-unit * 2) !important;
    margin-bottom: ($ln-spacing-unit * 2) !important;
  }

  .ln-u-push-sides\*3 {
    margin-right: ($ln-spacing-unit * 3) !important;
    margin-bottom: ($ln-spacing-unit * 3) !important;
  }

  .ln-u-push-sides\*4 {
    margin-right: ($ln-spacing-unit * 4) !important;
    margin-bottom: ($ln-spacing-unit * 4) !important;
  }
}

@if $use-ln-push-ends {
  .ln-u-push-ends {
    margin-top: $ln-spacing-unit !important;
    margin-bottom: $ln-spacing-unit !important;
  }

  .ln-u-push-ends\*1\/4 {
    margin-top: ($ln-spacing-unit / 4) !important;
    margin-bottom: ($ln-spacing-unit / 4) !important;
  }

  .ln-u-push-ends\*1\/2 {
    margin-top: ($ln-spacing-unit / 2) !important;
    margin-bottom: ($ln-spacing-unit / 2) !important;
  }

  .ln-u-push-ends\*2 {
    margin-top: ($ln-spacing-unit * 2) !important;
    margin-bottom: ($ln-spacing-unit * 2) !important;
  }

  .ln-u-push-ends\*3 {
    margin-top: ($ln-spacing-unit * 3) !important;
    margin-bottom: ($ln-spacing-unit * 3) !important;
  }

  .ln-u-push-ends\*4 {
    margin-top: ($ln-spacing-unit * 4) !important;
    margin-bottom: ($ln-spacing-unit * 4) !important;
  }
}

@if $use-ln-flush {
/* stylelint-disable */
/// Removes any margin from the specified side
/// @name Flush
/// @example markup
///   <div class="ln-u-flush">ln-u-flush</div>
///   <div class="ln-u-flush-top">ln-u-flush-top</div>
///   <div class="ln-u-flush-right">ln-u-flush-right</div>
///   <div class="ln-u-flush-bottom">ln-u-flush-bottom</div>
///   <div class="ln-u-flush-left">ln-u-flush-bottom</div>
///   <div class="ln-u-flush-sides">ln-u-flush-sides</div>
///   <div class="ln-u-flush-ends">ln-u-flush-ends</div>
/* stylelint-enable */
  .ln-u-flush {
    margin: 0 !important;
  }
}

@if $use-ln-flush-top {
  .ln-u-flush-top {
    margin-top: 0 !important;
  }
}

@if $use-ln-flush-right {
  .ln-u-flush-right {
    margin-right: 0 !important;
  }
}

@if $use-ln-flush-bottom {
  .ln-u-flush-bottom {
    margin-bottom: 0 !important;
  }
}

@if $use-ln-flush-left {
  .ln-u-flush-left {
    margin-left: 0 !important;
  }
}

@if $use-ln-flush-sides {
  .ln-u-flush-sides {
    margin-right: 0 !important;
    margin-left: 0 !important;
  }
}

@if $use-ln-flush-ends {
  .ln-u-flush-ends {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
}
