#globalMessages {
  position: relative;

  .icon,
  .itemsRemoved .icon {
    text-align: left;
    display: block;
    line-height: 2.5rem;
  }

  .information_message.negative,
  .itemsRemoved {
    display: block;
    height: 48px;
    background-color: $tu-color-wild-sand;
    color: $tu-input-error;
    font-weight: 700;
    padding-left: 30px;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 48px;
      font-size: 1rem;
      line-height: 2.4;
      margin-right: 0.5rem;
      background: $tu-input-error;
      color: $ln-color-white;
      text-align: center;
      font-family: Texta, serif;
      content: '!';
      z-index: 2;
    }
  }
}

.tu-c-search__results-header {
  margin-bottom: 1rem;
  position: relative;
  padding: 1rem;
  background: $tu-color-quill-gray;
}
