@mixin background-image-blocks($blocks) {
  $sizes: ();
  $positions: ();
  $elements: ();

  $loader-color: $tu-color-quill-gray;
  $loader-element: linear-gradient($loader-color, $loader-color);

  @for $i from 1 through length($blocks) {
    $block: nth($blocks, $i);
    $elements: append($elements, $loader-element, comma);
    $sizes: append($sizes, nth($block, 1), comma);
    $positions: append($positions, nth($block, 2), comma);
  }

  background-image: $elements;
  background-size: $sizes;
  background-position: $positions;
  background-repeat: no-repeat;
  animation: loading-summary 0.8s linear 0.25s infinite;
}

@keyframes loading-summary {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }

  95% {
    opacity: 1;
  }
}
