%lozenge-icon {
  @each $key, $value in map-get($ln-font-sizes-pixels, 'h3') {
    @if $key == 'xs' {
      font-size: $value;
    }
  }
}

$lozenge-size: $ln-size-def;
$lozenge-size-sm: 30px;

.tu-c-lozenge {
  background-color: $ln-color-white;
  color: $ln-color-black;
  display: inline-flex;
  align-items: center;
  font-family: $ln-font-family-brand;
  min-height: $lozenge-size;
  border-radius: ($lozenge-size / 2);
  padding-right: $ln-spacing-unit;
  text-decoration: none !important;

  &::before {
    display: inline-block;
    font-weight: $ln-font-weight-regular;
    margin-right: $ln-spacing-unit;
    text-decoration: none !important;
    min-width: $lozenge-size;
    @extend %lozenge-icon;
    @include ln-circle($lozenge-size);

    @include ln-respond-to('sm') {
      margin-right: $ln-spacing-unit-half;
    }
  }

  &--small {
    min-height: $lozenge-size-sm;
    padding: 0 0.5rem 0 0;

    &::before {
      display: inline-block;
      font-weight: $ln-font-weight-regular;
      min-width: $lozenge-size-sm;
      margin-right: $ln-spacing-unit-half;
      @extend %lozenge-icon;
      @include ln-circle($lozenge-size-sm);
    }
  }

  &--gray {
    background-color: $tu-color-wild-sand;
  }

  &--warning {
    &::before {
      min-width: $lozenge-size-sm;
      margin-right: $ln-spacing-unit-half;
      @include ln-circle(
        $lozenge-size-sm,
        $ln-color-black,
        $tu-color-tangerine-yellow
      );
    }
  }

  .ln-o-flyout & {
    text-decoration: underline !important;
    cursor: pointer;

    &::before {
      text-decoration: none;
    }
  }
}
