$ln-font-family-brand: Texta, Arial, sans-serif;
$ln-font-family-base: Texta, Arial, sans-serif;

$ln-font-keys: (hero, h1, h2, h3, h4, h5, h6, cta, body, body-sm);

$ln-font-sizes-pixels: (
  hero: (
    def: 48px,
    xs: 55px,
    xl: 55px,
  ),
  h1: (
    def: 30px,
    xs: 36px,
    xl: 36px,
  ),
  h2: (
    def: 22px,
    xs: 28px,
    xl: 28px,
  ),
  h3: (
    def: 18px,
    xs: 24px,
    xl: 24px,
  ),
  h4: (
    def: 18px,
    xs: 20px,
    xl: 20px,
  ),
  h5: (
    def: 18px,
    xs: 20px,
    xl: 20px,
  ),
  h6: (
    def: 16px,
    xs: 16px,
    xl: 16px,
  ),
  cta: (
    def: 16px,
  ),
  body: (
    def: 16px,
    xs: 16px,
  ),
  body-sm: (
    def: 16px,
  ),
);

$ln-font-sizes: ln-px-to-rem-font-sizes($ln-font-sizes-pixels);

$ln-line-heights: (
  hero: 5/4,
  h1: 40/36,
  h2: 30/28,
  h3: 25/24,
  h4: 25/20,
  h5: 25/20,
  h6: 20/16,
  cta: 20/16,
  body: 20/16,
  body-sm: 20/16,
);

$ln-font-families: (
  hero: $ln-font-family-brand,
  h1: $ln-font-family-brand,
  h2: $ln-font-family-brand,
  h3: $ln-font-family-brand,
  h4: $ln-font-family-brand,
  h5: $ln-font-family-brand,
  h6: $ln-font-family-base,
  cta: $ln-font-family-brand,
  body: $ln-font-family-brand,
  body-sm: $ln-font-family-brand,
);

$ln-font-weight-regular: 400;
$ln-font-weight-medium: 700;
$ln-font-weight-bold: 900;

$ln-font-weights: (
  hero: $ln-font-weight-medium,
  h1: $ln-font-weight-bold,
  h2: $ln-font-weight-bold,
  h3: $ln-font-weight-medium,
  h4: $ln-font-weight-bold,
  h5: $ln-font-weight-medium,
  h6: $ln-font-weight-medium,
  cta: $ln-font-weight-bold,
  body: $ln-font-weight-regular,
  body-sm: $ln-font-weight-regular,
);

$ln-font-size-base: 'body';
$ln-line-height-base: 'body';

$ln-line-height-base-px: ln-line-height-to-px-sizes(
  map-get($ln-line-heights, $ln-line-height-base),
  map-get($ln-font-sizes-pixels, $ln-font-size-base)
);

$tu-font-letterspacing-wide: 2px;
