////
/// Globals
/// @group settings/globals
////

$ln-browser-font-size-default: 16;
$ln-browser-font-size-default-px: $ln-browser-font-size-default * 1px;

/// Default spacing unit used throughout Luna, on the grid, spaced lists, utilities.
/// Has calculated variants for `-half` and `-x2` to `-x5`.
/// @type Length
$ln-spacing-unit: 1rem !default;
$ln-spacing-unit-quarter: ($ln-spacing-unit * 0.25);
$ln-spacing-unit-half: ($ln-spacing-unit * 0.5);
$ln-spacing-unit-double: ($ln-spacing-unit * 2);
$ln-spacing-unit-x2: $ln-spacing-unit-double;
$ln-spacing-unit-x3: ($ln-spacing-unit * 3);
$ln-spacing-unit-x4: ($ln-spacing-unit * 4);
$ln-spacing-unit-x5: ($ln-spacing-unit * 5);

/// A Sass map containing all of the media query breakpoints specified by Luna. Many components /
/// objects such as the grid, flag will iterate over this map during the Sass compilation.
/// Consumers can add additional properties to the map.
/// @type Map
/// @prop {Length} breakpoint [width]
$ln-breakpoints: (
  xs: 460px,
  sm: 720px,
  md: 960px,
  lg: 1200px,
  xl: 1400px
) !default;

$ln-unit: 8;
$ln-spacing-unit-size-at-def: $ln-unit * 1.5;  /* 12 */
$ln-spacing-unit-size-at-xs: $ln-unit * 2;     /* 16 */
$ln-spacing-unit-size-at-sm: $ln-unit * 2.5;   /* 20 */

/// A Sass map that contains percentages that are used to set the base-font-size in REM on the root
/// HTML node. This influences the computed value of `ln-spacing-unit` ultimately resulting in it
/// decreasing on smaller viewports at set breakpoints.
/// @type Map
/// @prop {Number} breakpoint [percentage]
$ln-spacing-percentages: (
  def: ($ln-spacing-unit-size-at-def / $ln-browser-font-size-default) * 100%,
  xs: ($ln-spacing-unit-size-at-xs / $ln-browser-font-size-default) * 100%,
  sm: ($ln-spacing-unit-size-at-sm / $ln-browser-font-size-default) * 100%
) !default;

/// A Sass map containing the varying gutter sizes to be compiled for the grid. The keys in the map
/// directly correlate to the class name used. Fore example to target the zero gutter width
/// `<div class="ln-o-grid ln-grid--gutter-zero />` the `zero` portion maps to the key in
/// `$ln-grid-gutter-sizes` consumers can add additional properties to the map.
/// @type Map
/// @prop {Length} breakpoint [size]
$ln-grid-gutter-sizes: (
  def: $ln-spacing-unit,
  zero: 0,
  sm: $ln-spacing-unit-half,
  lg: $ln-spacing-unit-x2,
  xl: $ln-spacing-unit-x3
) !default;

/// A Sass map containing spacing sizes made available for `<ul class="ln-o-[type]-list">`.
/// @type Map
/// @prop {Length} breakpoint [size]
$ln-list-spacing: (
  def: $ln-spacing-unit
) !default;

/// A string that is used for loading Luna assets such as the Mary Ann fonts. By default it's value is assets. Consumers will likely have to change this variable to match the path to the assets they import from Luna.
/// @type String
$ln-assets-url: 'assets' !default;

// Components

/// Default minimum height / width used by components
/// @type Length
$ln-size-def: 50px !default;

/// Height / width used by `--deep` modifiers to display a larger variant of a component
/// @type Length
$ln-size-lg: 90px !default;

/// Named breakpoint at which `$ln-size-lg` should take effect on components
/// @type Breakpoint
$ln-size-lg-breakpoint: 'min-md' !default;

/// Size of coloured bar used by certain components to denote state
/// @type Length
$ln-status-bar-width: 5px !default;

/// Consistent border-radius for buttons, text-inputs and various other components.
/// @type Length
$ln-border-radius: 0 !default;

/// Consistent transiton durations.
/// @type Time
$ln-transition-duration: 0.2s !default;

/// Maximum width of the object `<div class="ln-o-site-wrap" />` that typically wraps content on a page
/// @type Length
$ln-site-wrap-max-width: ln-px-to-rem-font-size(1400px, 'sm') !default;

/// Maximum width of the object & modifier `<div class="ln-o-site-wrap ln-o-site-wrap--squash" />`
/// wrapping page content
/// @type Length
$ln-site-wrap-squeeze-max-width: ln-px-to-rem-font-size(1100px, 'sm') !default;

/// Maximum width of the object & modifier `<div class="ln-o-site-wrap ln-o-site-wrap--large" />`
/// wrapping page content
/// @type Length
$ln-site-wrap-large-max-width: ln-px-to-rem-font-size(1800px, 'sm') !default;
