.tu-c-tile-selector {
  .tile {
    width: 50px;
    height: 50px;
    line-height: 50px;
    margin-right: 0.5rem;
    background-color: $ln-color-white;
  }

  .selectable {
    cursor: pointer;
  }
  @include ln-respond-to('sm') {
    .selectable:hover {
      border: 2px solid $ln-color-black !important;
      line-height: 48px;
    }
  }

  .disabled {
    position: relative;
    overflow: hidden;
    color: $tu-color-boulder;

    &::after {
      position: absolute;
      content: '';
      background: $tu-color-quill-gray;
      display: block;
      width: 1px;
      height: 71px;
      transform: rotate(45deg);
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }

  .selected {
    border: 2px solid $ln-color-black !important;
    font-weight: 600;
    line-height: 48px;
  }
}
