@if $use-ln-label {
  $ln-label-padding-bottom: $ln-spacing-unit-half !default;

  .ln-c-label {
    @include ln-font-size('h5');
    font-family: $ln-font-family-brand;
    font-weight: bold;
    color: $ln-color-grey-dark;
    margin-bottom: $ln-label-padding-bottom;
    display: inline-block;
  }

  .ln-c-label__info {
    color: $ln-color-grey;
  }
}
