// form field wrapper max width
$form-max-width: 560px;

.ln-c-form-group {
  display: flex;
  flex-flow: column;
  margin-bottom: 0;

  &--flush {
    margin-bottom: 0;
  }

  @include ln-respond-to('max-sm') {
    padding-top: $ln-spacing-unit;

    &.paymentContainer {
      padding-top: 0;
    }
  }
}

.tu-c-form-field-input-error {
  color: $ln-color-error;
}

.tu-c-form-field-wrapper {
  position: relative;
  margin: 0 auto;
  max-width: $form-max-width;

  @include ln-respond-to('min-sm') {
    width: $form-max-width / 2;
  }
}
