////
/// @group utilities/padding
////

@if $use-ln-soft {
/* stylelint-disable */
/// Adds `$ln-spacing-unit` padding to the specified side, with multiples of `$ln-spacing-unit` available
/// @name Soft
/// @example markup - Default
///   <div class="ln-u-soft">ln-u-soft</div>
///   <div class="ln-u-soft-top">ln-u-soft-top</div>
///   <div class="ln-u-soft-right">ln-u-soft-right</div>
///   <div class="ln-u-soft-bottom">ln-u-soft-bottom</div>
///   <div class="ln-u-soft-left">ln-u-soft-bottom</div>
///   <div class="ln-u-soft-sides">ln-u-soft-sides</div>
///   <div class="ln-u-soft-ends">ln-u-soft-ends</div>
/// @example markup - 1 / 4 x spacing unit
///   <div class="ln-u-soft*1/4">ln-u-soft*1/4</div>
///   <div class="ln-u-soft-top*1/4">ln-u-soft-top*1/4</div>
///   <div class="ln-u-soft-right*1/4">ln-u-soft-right*1/4</div>
///   <div class="ln-u-soft-bottom*1/4">ln-u-soft-bottom*1/4</div>
///   <div class="ln-u-soft-left*1/4">ln-u-soft-bottom*1/4</div>
///   <div class="ln-u-soft-sides*1/4">ln-u-soft-sides*1/4</div>
///   <div class="ln-u-soft-ends*1/4">ln-u-soft-ends*1/4</div>
/// @example markup - 1 / 2 x spacing unit
///   <div class="ln-u-soft*1/2">ln-u-soft*1/2</div>
///   <div class="ln-u-soft-top*1/2">ln-u-soft-top*1/2</div>
///   <div class="ln-u-soft-right*1/2">ln-u-soft-right*1/2</div>
///   <div class="ln-u-soft-bottom*1/2">ln-u-soft-bottom*1/2</div>
///   <div class="ln-u-soft-left*1/2">ln-u-soft-bottom*1/2</div>
///   <div class="ln-u-soft-sides*1/2">ln-u-soft-sides*1/2</div>
///   <div class="ln-u-soft-ends*1/2">ln-u-soft-ends*1/2</div>
/// @example markup - 2 x spacing unit
///   <div class="ln-u-soft*2">ln-u-soft*2</div>
///   <div class="ln-u-soft-top*2">ln-u-soft-top*2</div>
///   <div class="ln-u-soft-right*2">ln-u-soft-right*2</div>
///   <div class="ln-u-soft-bottom*2">ln-u-soft-bottom*2</div>
///   <div class="ln-u-soft-left*2">ln-u-soft-bottom*2</div>
///   <div class="ln-u-soft-sides*2">ln-u-soft-sides*2</div>
///   <div class="ln-u-soft-ends*2">ln-u-soft-ends*2</div>
/// @example markup - 3 x spacing unit
///   <div class="ln-u-soft*3">ln-u-soft*3</div>
///   <div class="ln-u-soft-top*3">ln-u-soft-top*3</div>
///   <div class="ln-u-soft-right*3">ln-u-soft-right*3</div>
///   <div class="ln-u-soft-bottom*3">ln-u-soft-bottom*3</div>
///   <div class="ln-u-soft-left*3">ln-u-soft-bottom*3</div>
///   <div class="ln-u-soft-sides*3">ln-u-soft-sides*3</div>
///   <div class="ln-u-soft-ends*3">ln-u-soft-ends*3</div>
/// @example markup - 4 x spacing unit
///   <div class="ln-u-soft*4">ln-u-soft*4</div>
///   <div class="ln-u-soft-top*4">ln-u-soft-top*4</div>
///   <div class="ln-u-soft-right*4">ln-u-soft-right*4</div>
///   <div class="ln-u-soft-bottom*4">ln-u-soft-bottom*4</div>
///   <div class="ln-u-soft-left*4">ln-u-soft-bottom*4</div>
///   <div class="ln-u-soft-sides*4">ln-u-soft-sides*4</div>
///   <div class="ln-u-soft-ends*4">ln-u-soft-ends*4</div>
/* stylelint-enable */
  .ln-u-soft { padding: $ln-spacing-unit !important; }
  .ln-u-soft\*1\/4 { padding: ($ln-spacing-unit / 4) !important; }
  .ln-u-soft\*1\/2 { padding: ($ln-spacing-unit / 2) !important; }
  .ln-u-soft\*2 { padding: ($ln-spacing-unit * 2) !important; }
  .ln-u-soft\*3 { padding: ($ln-spacing-unit * 3) !important; }
  .ln-u-soft\*4 { padding: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-soft-top {
  .ln-u-soft-top { padding-top: $ln-spacing-unit !important; }
  .ln-u-soft-top\*1\/4 { padding-top: ($ln-spacing-unit / 4) !important; }
  .ln-u-soft-top\*1\/2 { padding-top: ($ln-spacing-unit / 2) !important; }
  .ln-u-soft-top\*2 { padding-top: ($ln-spacing-unit * 2) !important; }
  .ln-u-soft-top\*3 { padding-top: ($ln-spacing-unit * 3) !important; }
  .ln-u-soft-top\*4 { padding-top: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-soft-right {
  .ln-u-soft-right { padding-right: $ln-spacing-unit !important; }
  .ln-u-soft-right\*1\/4 { padding-right: ($ln-spacing-unit / 4) !important; }
  .ln-u-soft-right\*1\/2 { padding-right: ($ln-spacing-unit / 2) !important; }
  .ln-u-soft-right\*2 { padding-right: ($ln-spacing-unit * 2) !important; }
  .ln-u-soft-right\*3 { padding-right: ($ln-spacing-unit * 3) !important; }
  .ln-u-soft-right\*4 { padding-right: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-soft-bottom {
  .ln-u-soft-bottom { padding-bottom: $ln-spacing-unit !important; }
  .ln-u-soft-bottom\*1\/4 { padding-bottom: ($ln-spacing-unit / 4) !important; }
  .ln-u-soft-bottom\*1\/2 { padding-bottom: ($ln-spacing-unit / 2) !important; }
  .ln-u-soft-bottom\*2 { padding-bottom: ($ln-spacing-unit * 2) !important; }
  .ln-u-soft-bottom\*3 { padding-bottom: ($ln-spacing-unit * 3) !important; }
  .ln-u-soft-bottom\*4 { padding-bottom: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-soft-left {
  .ln-u-soft-left { padding-left: $ln-spacing-unit !important; }
  .ln-u-soft-left\*1\/4 { padding-left: ($ln-spacing-unit / 4) !important; }
  .ln-u-soft-left\*1\/2 { padding-left: ($ln-spacing-unit / 2) !important; }
  .ln-u-soft-left\*2 { padding-left: ($ln-spacing-unit * 2) !important; }
  .ln-u-soft-left\*3 { padding-left: ($ln-spacing-unit * 3) !important; }
  .ln-u-soft-left\*4 { padding-left: ($ln-spacing-unit * 4) !important; }
}

@if $use-ln-soft-sides {
  .ln-u-soft-sides {
    padding-left: $ln-spacing-unit !important;
    padding-right: $ln-spacing-unit !important;
  }

  .ln-u-soft-sides\*1\/4 {
    padding-left: ($ln-spacing-unit / 4) !important;
    padding-right: ($ln-spacing-unit / 4) !important;
  }

  .ln-u-soft-sides\*1\/2 {
    padding-left: ($ln-spacing-unit / 2) !important;
    padding-right: ($ln-spacing-unit / 2) !important;
  }

  .ln-u-soft-sides\*2 {
    padding-left: ($ln-spacing-unit * 2) !important;
    padding-right: ($ln-spacing-unit * 2) !important;
  }

  .ln-u-soft-sides\*3 {
    padding-left: ($ln-spacing-unit * 3) !important;
    padding-right: ($ln-spacing-unit * 3) !important;
  }

  .ln-u-soft-sides\*4 {
    padding-left: ($ln-spacing-unit * 4) !important;
    padding-right: ($ln-spacing-unit * 4) !important;
  }
}

@if $use-ln-soft-ends {
  .ln-u-soft-ends {
    padding-bottom: $ln-spacing-unit !important;
    padding-top: $ln-spacing-unit !important;
  }

  .ln-u-soft-ends\*1\/4 {
    padding-bottom: ($ln-spacing-unit / 4) !important;
    padding-top: ($ln-spacing-unit / 4) !important;
  }

  .ln-u-soft-ends\*1\/2 {
    padding-bottom: ($ln-spacing-unit / 2) !important;
    padding-top: ($ln-spacing-unit / 2) !important;
  }

  .ln-u-soft-ends\*2 {
    padding-bottom: ($ln-spacing-unit * 2) !important;
    padding-top: ($ln-spacing-unit * 2) !important;
  }

  .ln-u-soft-ends\*3 {
    padding-bottom: ($ln-spacing-unit * 3) !important;
    padding-top: ($ln-spacing-unit * 3) !important;
  }

  .ln-u-soft-ends\*4 {
    padding-bottom: ($ln-spacing-unit * 4) !important;
    padding-top: ($ln-spacing-unit * 4) !important;
  }
}

@if $use-ln-hard {
/* stylelint-disable */
/// Removes any margin from the specified side
/// @name Hard
/// @example markup
///   <div class="ln-u-hard">ln-u-hard</div>
///   <div class="ln-u-hard-top">ln-u-hard-top</div>
///   <div class="ln-u-hard-right">ln-u-hard-right</div>
///   <div class="ln-u-hard-bottom">ln-u-hard-bottom</div>
///   <div class="ln-u-hard-left">ln-u-hard-bottom</div>
///   <div class="ln-u-hard-sides">ln-u-hard-sides</div>
///   <div class="ln-u-hard-ends">ln-u-hard-ends</div>
/* stylelint-enable */
  .ln-u-hard {
    padding: 0 !important;
  }
}

@if $use-ln-hard-top {
  .ln-u-hard-top {
    padding-top: 0 !important;
  }
}

@if $use-ln-hard-right {
  .ln-u-hard-right {
    padding-right: 0 !important;
  }
}

@if $use-ln-hard-bottom {
  .ln-u-hard-bottom {
    padding-bottom: 0 !important;
  }
}

@if $use-ln-hard-left {
  .ln-u-hard-left {
    padding-left: 0 !important;
  }
}

@if $use-ln-hard-sides {
  .ln-u-hard-sides {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

@if $use-ln-hard-ends {
  .ln-u-hard-ends {
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
}
