.tu-c-cpm-email-sign-up__email-input-wrapper {
  position: relative;
  max-width: 300px;
  margin: 0 auto;
  width: 100%;

  @include ln-respond-to('max-xs') {
    max-width: none;
  }
}

.tu-c-cpm-email-sign-up__error-input-wrapper {
  padding: 10px 20px;
  width: 100%;
}

.tu-c-cpm-email-sign-up__button {
  @include ln-respond-to('sm') {
    display: inline-flex;
  }
  @include ln-respond-to('md') {
    display: inline-flex;
  }
}

.tu-c-cpm-email-sign-up__email-input-error {
  color: $ln-color-error;
  width: 100%;
  margin: 0 auto;
  max-width: 19rem;

  @include ln-respond-to('sm') {
    max-width: 15rem;
  }

  @include ln-respond-to('max-xs') {
    max-width: none;
    padding: 0 1.5rem;
  }
}

.tu-c-cpm-email-sign-up__email-error-container {
  padding-right: 50px;
  position: relative;

  .tu-c-cpm-email-sign-up--error & {
    &::after {
      right: 50px;
    }
  }
}

.tu-c-cpm-email-sign-up__tu-u-submit-button {
  position: absolute;
  height: 100%;
  background: $ln-color-white;
  z-index: 1;
  max-height: 4.2rem;
  top: 0;
  right: 0;

  @include ln-respond-to('md') {
    right: -2.5rem;
  }

  @include ln-respond-to('sm') {
    right: 0;
  }

  @include ln-respond-to('xs') {
    max-height: 3.2rem;
  }
}

.tu-c-cpm-email-sign-up__tu-u-form-submitted {
  overflow: hidden;
}

.tu-c-cpm-email-sign-up__tu-u-email-confirm {
  position: absolute;
  width: 0;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  background: $tu-color-jungle-green-dark;
  color: $ln-color-white;
  right: 0;
  transition: width 0.25s;
  overflow: hidden;
  height: 50px;
  bottom: 0;

  .tu-c-cpm-email-sign-up--success & {
    width: 100%;
  }
}

.tu-c-cpm-email-sign-up__tu-u-error-wrapper {
  &::before {
    content: '';
    @include ln-tick(0.5rem, $ln-color-white, 0.95rem, 0.65rem);
    position: relative;
    width: 0.45rem;
    height: 0.85rem;
    top: -2px;
    left: -0.75rem;
    display: inline-block;

    @include ln-respond-to('max-sm') {
      top: 0;
      height: 1rem;
    }

    @include ln-respond-to('max-xs') {
      top: 0;
      height: 1.2rem;
      width: 0.6rem;
    }
  }
}

.tu-c-cpm-email-sign-up__tu-u-sign-up {
  padding-right: 0;
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  width: 3rem;
  display: block;

  @include ln-respond-to('sm') {
    right: 2.5rem;
  }
}

.tu-c-cpm-email-sign-up__label {
  margin: 0 auto 0.25rem;
  width: 100%;
}

.tu-c-cpm-email-sign-up {
  display: inline-flex;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  width: 100%;
  justify-content: center;
}

.tu-c-cpm-email-sign-up__tu-u-button-wrapper {
  border: 2px solid $tu-color-black;
  position: absolute;
  overflow: hidden;
  justify-content: flex-end;
  right: 0;
  background-color: $ln-color-white;
  transition: all 0.3s ease-in-out;
  height: auto;
  padding: 0.45rem;

  @include ln-respond-to('sm') {
    padding: 0.375rem;
  }

  @include ln-respond-to('max-xs') {
    padding: 0.6rem;
    width: 50px;
    height: 50px;
  }

  @include ln-respond-to('min-md') {
    width: 2.5rem;

    &:hover {
      width: 6.75rem;
    }
  }
}
