@if $use-ln-button {
/* stylelint-disable */
/// Primary and secondary cosmetic styling can be composed with larger
/// sizing and both types with the full-width and/or disabled.
/// @name Button
/// @group components
/// @example markup - Primary
///   <button class="ln-c-button ln-c-button--primary">Primary</button>
/// @example markup - Secondary
///   <button class="ln-c-button ln-c-button--secondary">Secondary</button>
/// @example markup - Deep
///   <button class="ln-c-button ln-c-button--primary ln-c-button--deep">Deep</button>
/// @example markup - Full width
///   <button class="ln-c-button ln-c-button--primary ln-c-button--full">Full width</button>
/// @example markup - Disabled
///   <button class="ln-c-button ln-c-button--primary" disabled>Disabled</button>
/// @example markup - Text button
///   <button class="ln-c-button ln-c-button--text-only">Text button</button>
/* stylelint-enable */
  .ln-c-button {
    @include ln-font('cta');

    min-height: $ln-size-def;
    min-width: $ln-size-def;
    padding: $ln-spacing-unit-half $ln-spacing-unit;
    border-radius: $ln-border-radius;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    background-color: transparent;

    // Only apply flex to non-buttons
    // https://github.com/philipwalton/flexbugs#9-some-html-elements-cant-be-flex-containers
    &:not(button) {
      display: inline-flex;
      align-items: center;
      justify-content: center;
    }
  }

  .ln-c-button--primary {
    color: $ln-color-button-primary;
    background-color: $ln-color-button-primary-background;

    &:hover,
    &:focus {
      color: $ln-color-button-primary;
      background-color: $ln-color-button-primary-background-hover;
    }
  }

  .ln-c-button--secondary {
    position: relative;
    color: $ln-color-button-secondary;
    background-color: $ln-color-button-secondary-background;

    &:hover,
    &:focus {
      color: $ln-color-button-secondary-hover;
      background-color: $ln-color-button-secondary-background-hover;
    }
  }

  /// @deprecated
  .ln-c-button--sm {
    @include ln-font-size('body-sm');
  }

  /// @deprecated
  .ln-c-button--lg {
    @include ln-font-size('h3');
  }

  .ln-c-button--deep {
    padding-top: $ln-spacing-unit;
    padding-bottom: $ln-spacing-unit;

    @include ln-respond-to($ln-size-lg-breakpoint) {
      min-height: $ln-size-lg;
    }
  }

  .ln-c-button--full {
    width: 100%;
  }

  .ln-c-button[disabled] {
    color: $ln-color-button-disabled;
    background-color: $ln-color-button-disabled-background;
    pointer-events: none;
    cursor: not-allowed;
  }

  .ln-c-button--text-only {
    background-color: transparent;
    border-bottom: 2px solid currentColor;
    color: $ln-color-black;
    min-height: 0;
    padding: 0;

    &:hover,
    &:focus {
      color: $ln-color-orange-dark;
    }
  }

  .ln-c-button--nowrap {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .ln-c-button--align-left {
    justify-content: flex-start;
    text-align: left;
  }

  .ln-c-button--align-right {
    justify-content: flex-end;
    text-align: right;
  }
}
