@if $use-ln-icon {
  .ln-c-icon {
    display: inline-block;
    vertical-align: middle;
    fill: currentColor;
    stroke: currentColor;
    height: map-get($ln-icon-sizes, 'def');
    width: map-get($ln-icon-sizes, 'def');
  }

  .ln-c-icon--left {
    margin-right: $ln-spacing-unit-half;
  }

  .ln-c-icon--right {
    margin-left: $ln-spacing-unit-half;
  }

  @each $key, $value in $ln-icon-sizes {
    @if $key != 'def' {
      .ln-c-icon--#{$key} {
        height: $value;
        width: $value;
      }
    }
  }
}
