$tu-pdp-selector-height: 44px;
$tu-pdp-selector-height--checked: $tu-pdp-selector-height + 4px;
$tu-pdp-selector-height--line: $tu-pdp-selector-height - 13px;
$tu-standalone: '.ln-c-standalone-link';

.tu-pdp__image {
  img {
    color: transparent;
    width: 100%;
    z-index: 2;
  }

  .tu-pdp__image-item img {
    opacity: 0;
  }
}

.tu-pdp__image-item .slick-loading {
  overflow: hidden;
  height: 0;
  padding-bottom: 136%;
}

.tu-pdp__information-selector {
  list-style: none;
  margin: 0;
}

.tu-pdp__information-selector-item {
  display: inline-block;
  margin-bottom: $ln-spacing-unit-half;
  margin-right: $ln-spacing-unit-half;

  .content {
    padding: 0;
  }
}

.tu-pdp__information-selector-link {
  background-color: $ln-color-white;
  background-position: 50%;
  background-repeat: no-repeat;
  border: 1px solid $tu-color-boulder;
  display: inline-block;
  width: $tu-pdp-selector-height;
  height: $tu-pdp-selector-height;
  position: relative;
  text-decoration: none;
  text-align: center;
  line-height: $tu-pdp-selector-height;

  .tu-pdp__information-selector--colour & {
    text-indent: 2000px;
  }
}

.tu-pdp__information-selector-list {
  a {
    @extend #{$tu-standalone};
  }
}

.tu-pdp__information-item .content {
  padding: 0;
}

.tu-pdp__information {
  @include ln-respond-to('sm') {
    position: sticky;
    top: 58px;
    float: right;
    z-index: 3;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      position: relative;
      top: 0;
      float: none;
    }
  }
}

.tu-pdp__image-item {
  cursor: url('data:image/png;base64,'),
    auto;
  position: relative;
  height: 100%;
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    cursor: pointer;
  }
}

.tu-pdp__image-caption {
  position: absolute;
  display: block;
  left: 0;
  right: 0;
  margin-left: 1rem;
  bottom: 0;
  padding: $ln-spacing-unit-half;
  text-align: center;
  color: $ln-color-white;
  background-color: rgba($tu-color-black, 0.8);

  &--red {
    background-color: rgba($ln-color-red, 0.8);
  }

  &--grey {
    background-color: rgba($tu-color-boulder, 0.8);
  }
}

.tu-pdp__size-guide {
  padding-top: $ln-spacing-unit;

  @include ln-respond-to('sm') {
    padding-top: $ln-spacing-unit-half;
  }
}

.tu-pdp__price {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @include ln-respond-to('max-sm') {
    padding-top: $ln-spacing-unit-half;
  }
}

input:checked + .tu-pdp__information-selector-link {
  font-weight: 700;

  &::before {
    border: 2px solid $ln-color-black;
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    width: $tu-pdp-selector-height--checked;
    height: $tu-pdp-selector-height--checked;
    z-index: 2;
  }
}

input:disabled + .tu-pdp__information-selector-link {
  color: $tu-color-boulder;
  cursor: not-allowed;

  &::before {
    content: '';
    width: $tu-pdp-selector-height;
    height: $tu-pdp-selector-height;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    background-image: repeating-linear-gradient(
      -45deg,
      $tu-color-quill-gray,
      $tu-color-quill-gray 2px,
      transparent 2px,
      transparent $tu-pdp-selector-height--line
    );
    z-index: 2;
  }
}
