@mixin ln-list-spacing($class, $side) {
  $modifier: '--spaced';
  @if $class == 'matrix-list' {
    $modifier: '';
  }
  @each $key, $value in $ln-list-spacing {
    $compound-class: 'ln-o-#{$class}#{$modifier}-#{$key}';

    @if $key == 'def' {
      $compound-class: 'ln-o-#{$class}#{$modifier}';
    }

    .#{$compound-class} {
      margin-#{$side}: -$value;
    }

    .#{$compound-class} > .ln-o-#{$class}__item {
      padding-#{$side}: $value;
    }
  }
}
