body {
  color: $tu-color-cape-cod;
}

ul,
ol,
dd {
  margin-left: 0;
}

video {
  max-width: 100%;
}

/* Remove ui-mobile style once jquery-mobile is removed */
.ui-mobile {
  min-height: 0 !important;
  height: initial !important;
}

.ln-c-main-content {
  padding: 0 $ln-spacing-unit;

  @include ln-respond-to('sm') {
    padding: 0;
  }
}

.tu-b-block {
  display: inherit;
  max-width: $tu-max-width;
  width: 100%;
  margin-right: auto;
  margin-left: auto;

  .tu-c-category & {
    min-width: auto;
    padding-left: $ln-spacing-unit;
    padding-right: $ln-spacing-unit;
  }

  &--sm {
    max-width: $tu-wrapper-width;
  }
}

a {
  color: $ln-color-links;

  &:hover,
  &:focus {
    color: $ln-color-links--hover;
  }
}

.tu-b-clearfix {
  @include ln-clearfix;
}

.tu-b-overlay {
  position: absolute;
  top: -7.3rem;
  padding-bottom: 60px;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  z-index: 90;

  @include ln-respond-to('xs') {
    top: -3.5rem;
  }
}

.tu-b-content {
  width: auto;
  max-width: 990px;
  background-color: transparent;
  margin: 0 auto;
  padding: 0.5rem;

  @include ln-respond-to('sm') {
    padding: 0;
  }
}

.tu-b-position {
  position: relative;
  z-index: 1;
}

.tu-b-form-maxwidth {
  max-width: $tu-form-maxwidth;
}

.tu-c-product-card .tu-b-brand-image-dimensions {
  display: inline-block;
  height: 20px;
  width: auto;
  vertical-align: middle;
}
