$map-height: 24rem;
$map-height-mobile: 32rem;

%max-width {
  @include ln-respond-to('sm') {
    max-width: 23rem;
  }
  max-width: 25rem;
}

%stores-list-container-max-width {
  @extend %max-width;
  @include ln-respond-to('sm') {
    max-width: 31rem;
  }
}

%align-center {
  margin-left: auto;
  margin-right: auto;
}

%margin-top {
  margin-top: $ln-spacing-unit * 2;
}

.tu-c-checkout-store-locator-root {
  background-color: $ln-color-white;
  padding: 2rem 0;
}

.tu-c-checkout-store-locator-lookup-form {
  @extend %max-width;
  @extend %align-center;
}

.tu-c-checkout-store-locator-back-to-delivery-button-container {
  @extend %margin-top;
  text-align: center;
}

.tu-c-checkout-store-locator-return-to-delivery-options-link {
  @extend %margin-top;
  text-align: center;
}

.tu-c-checkout-store-locator-stores-list-container {
  @extend %stores-list-container-max-width;
  @extend %align-center;
  @extend %margin-top;
}

.tu-c-checkout-store-locator-store-details-overlay .overlay-container {
  background: rgba(0, 0, 0, 0.25);
  z-index: 10000;
}

.tu-c-checkout-store-locator-store-details-overlay .close-button-container {
  margin-left: 0;
}

.tu-c-checkout-store-locator-store-details-overlay .close-button-container svg {
  background-color: transparent;
  fill: $tu-color-black;
  margin-top: 1.25 * $ln-spacing-unit;
  margin-right: 0.75 * $ln-spacing-unit;
  margin-bottom: 0;
  margin-left: -2.5 * $ln-spacing-unit;
  width: 3 * $ln-spacing-unit;
  height: 3 * $ln-spacing-unit;

  @include ln-respond-to('sm') {
    width: 2 * $ln-spacing-unit;
    height: 2 * $ln-spacing-unit;
    margin-top: 1.5 * $ln-spacing-unit;
  }
}

.tu-c-checkout-store-locator-store-details-overlay .overlay-content {
  display: flex;
}

.tu-c-checkout-store-locator-store-details-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: $ln-spacing-unit * 1.5 $ln-spacing-unit 0;
  color: $ln-color-black;
  background-color: $ln-color-white;
  height: 100vh;
  width: 100vw;

  @include ln-respond-to('sm') {
    height: auto;
    max-height: 90vh;
    max-width: 32rem;
  }
}

.tu-c-checkout-store-locator-store-details-name {
  padding-right: 3 * $ln-spacing-unit;
}

.tu-c-checkout-store-locator-store-details-content {
  overflow: auto;
  height: 100%;
}

.tu-c-checkout-store-locator-store-details-address,
.tu-c-checkout-store-locator-store-details-telephone {
  margin-bottom: $ln-spacing-unit-half;
}

.tu-c-checkout-store-locator-store-details-opening-times {
  margin-bottom: $ln-spacing-unit-half;
  width: auto;
}

.tu-c-checkout-store-locator-store-details-opening-times td {
  padding: 0 $ln-spacing-unit 0 0;
  border: none;
}

.tu-c-checkout-store-locator-store-details-opening-times:last-child {
  padding: 0;
}

.tu-c-checkout-store-locator-store-details-special-opening-times-header {
  font-weight: bold;
  margin-bottom: $ln-spacing-unit-half;
}

.tu-c-checkout-store-locator-store-details-close {
  margin: 2 * $ln-spacing-unit 0;
  text-align: center;
}

.tu-c-checkout-store-locator-sticky-container {
  @include ln-respond-to('sm') {
    position: sticky;
    top: 0;
  }
}

.tu-c-checkout-store-locator-map {
  @include ln-respond-to('sm') {
    height: $map-height;
  }
  height: $map-height-mobile;
  width: 100%;

  &-loading {
    opacity: 0.5;
  }
}

.tu-c-checkout-store-locator-map-view-store-cards {
  @include ln-respond-to('sm') {
    display: none;
  }
  display: block;
}

.tu-c-checkout-store-locator-show-more-results {
  display: flex;
  justify-content: flex-end;
  margin-top: $ln-spacing-unit * 1.25;

  @include ln-respond-to('sm') {
    margin-top: $ln-spacing-unit * 0.75;
  }

  button {
    /* Reset button background in IE 11 */
    background: transparent;
  }
}

.tu-c-checkout-store-locator-no-results-found {
  @extend %max-width;
  @extend %align-center;
  @extend %margin-top;
}

.tu-c-checkout-store-locator-proceed-to-summary {
  @extend %max-width;
  @extend %align-center;
  text-align: center;
  border-top: 1px solid $tu-color-quill-gray;
  margin-top: $ln-spacing-unit * 3.5;
  padding-top: $ln-spacing-unit * 3.5;

  @include ln-respond-to('sm') {
    margin-top: $ln-spacing-unit * 2;
    padding-top: $ln-spacing-unit * 2;
  }
}

.tu-c-checkout-store-locator-previous-store {
  @extend %max-width;
  @extend %align-center;

  .horizontal-line {
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
    border-top: 1px solid $tu-color-quill-gray;
    color: $ln-color-black;
    font-weight: bold;
    text-align: center;
    margin: $ln-spacing-unit * 2 0;
    overflow: visible;

    @include ln-respond-to('sm') {
      margin: $ln-spacing-unit * 1.5 0;
    }
  }

  .horizontal-line::after {
    content: 'OR';
    position: relative;
    background-color: $ln-color-white;
    padding: 0 $ln-spacing-unit;
  }
}

.tu-c-checkout-store-locator-stores-list-container address,
.tu-c-checkout-store-locator-previous-store address {
  font-style: normal;
  margin: 0;
}
