.tu-brand-size-guide-container {
  &--hidden {
    visibility: hidden;
  }

  .overlay-container {
    z-index: 9999;
    overflow: auto;
    align-items: flex-start;
  }

  .overlay-container > div {
    margin: $ln-spacing-unit;

    @include ln-respond-to('sm') {
      margin: ($ln-spacing-unit * 2) $ln-spacing-unit;
    }
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .image-component img {
      width: 100%;
      max-width: 900px;
    }
  }
}
