$product-min-width: 140px;
$product-min-width-sm: 125px;
$product-min-width-xs: 100px;

.tu-t-checkout-progress__link {
  display: inline-block;
  line-height: 2.5rem;
  background-color: $ln-color-white;
  position: relative;
  text-decoration: none;

  span {
    display: none;

    @include ln-respond-to('sm') {
      display: inline-block;
      padding: 0 ($ln-spacing-unit-x3 / 2) 0 0;
    }
  }

  &::before {
    counter-increment: progress; /* Increment the value of section counter by 1 */
    content: counter(progress);
    display: inline-block;
    width: 3rem;

    @include ln-respond-to('sm') {
      margin-right: ($ln-spacing-unit-x3 / 2);
      border-right: 1px solid $tu-color-quill-gray;
    }
  }

  &--completed {
    &::before {
      background-color: $tu-input-success;
      color: $tu-input-success;
    }

    &::after {
      content: '';
      @include ln-tick(0.5rem, $ln-color-white, 1.25rem, 0.6rem);
    }
  }

  &--active {
    border: 1px solid $ln-color-black;

    span {
      display: inline-block;
      padding: 0 ($ln-spacing-unit-x3 / 2) 0 0;
    }

    &::before {
      border-right: 1px solid $ln-color-black;
      margin-right: ($ln-spacing-unit-x3 / 2);
      padding: 0 $ln-spacing-unit;
    }
  }
}

.tu-t-checkout-form-wrapper {
  border: 1px solid $tu-color-quill-gray;
  padding: 0.2rem 2rem;

  @include ln-respond-to('sm') {
    margin: 0 -2rem;
  }

  button:focus {
    outline: none;
  }
}

.tu-t-checkout-icon {
  transition: transform 0.5s;

  &--close {
    transform: rotate(45deg);
    transition: transform 0.5s;
  }
}

.tu-t-checkout-progress__group {
  counter-reset: progress;
  padding-top: $ln-spacing-unit;

  @include ln-respond-to('sm') {
    padding-top: $ln-spacing-unit-x2;
  }

  .ln-c-icon {
    margin: 0 $ln-spacing-unit-half;
  }
}

.tu-t-checkout-add-address {
  max-width: 300px;
  margin: 0 auto;
}

.tu-t-checkout-input-wrapper {
  max-width: none !important;
}

.tu-t-checkout-delivery-header {
  align-items: center;
  cursor: pointer;
}

.tu-t-checkout-delivery-header__index {
  min-width: $product-min-width-xs;
  border-right: 1px solid $tu-color-quill-gray;
  padding: $ln-spacing-unit 1.5rem;
  margin-right: $ln-spacing-unit;

  @include ln-respond-to('xs') {
    min-width: $product-min-width-sm;
  }

  @include ln-respond-to('sm') {
    min-width: $product-min-width;
  }
}

.tu-t-checkout-delivery-header__icon {
  padding: $ln-spacing-unit;
  text-align: right;
  margin-left: $ln-spacing-unit;

  @include ln-respond-to('sm') {
    min-width: $product-min-width;
  }

  .ln-c-icon {
    transition: transform 0.5s;
    transform: rotate(180deg);
  }

  &--show {
    .ln-c-icon {
      transform: rotate(45deg);
    }
  }
}

.tu-t-checkout-delivery__image {
  max-width: 100px;
  width: 100%;
}

.tu-t-checkout-delivery__figure {
  flex-grow: 2;
}

.tu-t-checkout__button-wrapper {
  text-align: center;
}

.tu-t-checkout {
  .ln-c-text-input {
    max-width: 100%;
  }

  .tu-t-checkout-select {
    @include ln-respond-to('xs') {
      max-width: 50%;
    }
  }
}

.tu-t-checkout-postcode-input {
  padding-top: 0 !important;
}

.tu-t-checkout-address__navigation {
  @include ln-respond-to('xs') {
    padding: 0.85rem;
  }

  @include ln-respond-to('sm') {
    padding: 0.75rem;
  }

  @include ln-checkout($tu-color-quill-gray, $ln-color-black);
  text-transform: uppercase;
  font-weight: $ln-font-weight-medium;
  letter-spacing: 1px;
  padding: 1.2rem;
  text-align: center;
}

.tu-t-address__item {
  @include ln-checkout($tu-color-quill-gray, $ln-color-black);
  position: relative;
  margin-bottom: $ln-spacing-unit;
}

.tu-t-address__label {
  &::after {
    content: '';
    display: inline-block;
    border-radius: 50%;
    border: 1px solid $tu-color-quill-gray;
    height: ($ln-spacing-unit-x3 / 2);
    width: ($ln-spacing-unit-x3 / 2);
    position: absolute;
    top: 1rem;
    right: 1rem;
  }

  .is-selected & {
    &::before {
      @include ln-tick(0.4rem, $ln-color-white, auto, 1.25rem);
      content: '';
      position: absolute;
      right: 1.525rem;
      z-index: 2;
    }

    &::after {
      border-width: 0;
      background-color: $tu-input-success;
    }
  }
}

.tu-t-checkout-flex-table {
  flex-wrap: wrap;
}

.tu-t-checkout-flex-table__item {
  padding: $ln-spacing-unit 0 0;

  &--head {
    background-color: $tu-color-wild-sand;
    padding: $ln-spacing-unit-half;
  }
}

.tu-t-store-list {
  counter-reset: map-pin-counter;
}

.tu-t-map-popoup {
  min-width: 25rem;
}
