$bg-color: $ln-color-white;
$border-color: $tu-color-quill-gray;
$selected-border-color: $tu-color-black;
$disabled-text-color: $tu-color-boulder;

.tu-c-fulfilment-dates {
  border-top: 1px solid $border-color;
  border-left: 1px solid $border-color;
}

.tu-c-fulfilment-date {
  position: relative;
  min-height: $ln-spacing-unit * 6;
  @include ln-respond-to('xs') {
    min-height: $ln-spacing-unit * 3.5;
  }
  background-color: $bg-color;
  border-right: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  cursor: pointer;

  &.stretch {
    flex-grow: 1;
  }

  &.selected::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    border: 2px solid $selected-border-color;
    margin: -1px;
  }

  &.disabled {
    color: $disabled-text-color;
    background: linear-gradient(
      to top left,
      $bg-color 0%,
      $bg-color calc(50% - 1px),
      $border-color 50%,
      $bg-color calc(50% + 1px),
      $bg-color 100%
    );
    cursor: not-allowed;
  }
}
