$transition-duration: 0.5s;
$nav-width: 55px;

.tu-c-mobile-nav-burger-icon {
  flex-direction: column;
  position: relative;
  left: 0;
  height: 50px;
  width: $nav-width;
  z-index: 9998;
  transition: $transition-duration;

  &.active {
    left: calc(100% - #{$nav-width});
    position: fixed;
    top: 0;
    background-color: $ln-color-white;
  }
}

.burger {
  width: 26px;
  height: 3px;
  margin: 2px auto;
  background-color: $ln-color-black;
  transition: $transition-duration;
}

.active {
  .burger {
    &:nth-of-type(1) {
      transform: translateX(1px) translateY(7px) rotate(45deg);
    }

    &:nth-of-type(2) {
      opacity: 0;
    }

    &:nth-of-type(3) {
      transform: translateX(1px) translateY(-7px) rotate(-45deg);
    }
  }
}
