$nav-width: 55px;
$tu-box-shadow: 0 $nav-width 10px 0 rgba(0, 0, 0, 0.1);
$border-color: #d9d9d6;
$transition-duration: 0.5s;

.tu-c-mobile-nav {
  @include ln-full-screen;
  width: calc(100% - #{$nav-width});
  background-color: $ln-color-white;
  transform: translateX(-100%);
  transition: transform $transition-duration;

  &.active {
    box-shadow: $tu-box-shadow;
    transform: translateX(0);
  }

  &__back-button {
    border-right: 1px solid $border-color;

    button {
      transform: rotate(0.5turn);
      outline: none;
    }
  }

  &__list-container {
    position: relative;

    &--list {
      position: absolute;
      width: 100%;
      height: 100%;
      overflow: auto;
      background: $ln-color-white;
    }

    &--level-one-item-image {
      height: 8rem;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }

    &--item {
      height: 3rem;
    }

    .ln-c-icon {
      stroke: none;
    }

    .slide-enter {
      opacity: 0;
      transform: translateX(-100%);
    }

    .slide-enter-active {
      opacity: 1;
      transform: translateX(0);
      transition: $transition-duration;
    }

    .slide-exit {
      opacity: 1;
      transform: translateX(0);
    }

    .slide-exit-active {
      opacity: 0;
      transform: translateX(-100%);
      transition: $transition-duration;
    }
  }
}

.tu-c-mobile-header {
  position: sticky;
  top: 0;
  width: 100%;
  background-color: $ln-color-white;
  z-index: 9998;
}

.tu-c-mobile-header ~ .ui-content {
  margin-top: 0;
}

.tu-c-mobile-header__container {
  height: 100%;
}

.tu-c-mobile-header__cart-count {
  position: absolute;
  padding-top: 7px;
}

.tu-c-mobile-header__nav-items {
  min-width: $nav-width;

  &--active {
    background-color: $ln-color-black;
  }
}

.tu-c-autocomplete {
  position: absolute;
  top: 0;
  z-index: 2;
}
