@if $use-ln-button-group {
  // deprecated cta naming convention

  .ln-c-cta-button-group,
  .ln-c-button-group {
    padding-top: $ln-spacing-unit;
    margin-bottom: $ln-spacing-unit;
  }

  .ln-c-cta-button-group__primary,
  .ln-c-button-group__primary {
    float: right;
  }

  .ln-c-cta-button-group__secondary,
  .ln-c-button-group__secondary {
    float: left;
  }
}
