$tu-box-shadow: 0 0 15px 5px rgba(50, 50, 50, 0.2);
$min-width: 400px;

.tu-c-forgot-password {
  @include ln-full-screen;
  right: -100%;
  overflow: auto;
  background-color: $ln-color-white;
  transition: right 0.5s ease;

  &__close-button {
    background-color: #f6f6f6;
    border-radius: 100%;

    &:focus {
      outline: none;
    }
  }

  @include ln-respond-to('xs') {
    min-width: $min-width;
    width: 40%;
  }

  &.active {
    box-shadow: $tu-box-shadow;
    right: 0;
  }
}
