%spinner {
  position: absolute;
  width: 100%;
  height: 100%;
  border: $ln-spacing-unit solid $tu-color-cape-cod;
  border-top-color: transparent;
  border-radius: 50%;
}

$tu-spinner-size: 100px;
$tu-bar-width: 6px;

@keyframes lds-rolling {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
  }

  20% {
    transform: scaleY(1);
  }
}

.tu-c-spinner-container {
  min-height: 200px;
  position: relative;
}

.tu-c-spinner {
  width: $tu-spinner-size !important;
  height: $tu-spinner-size;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
  z-index: 1;
  display: none;

  .slick-active &,
  &--active {
    display: block;
  }
}

.tu-c-spinner__inner {
  @extend %spinner;
  animation: lds-rolling 1.5s linear infinite;
  top: 0;
  left: 0;

  &::after {
    @extend %spinner;
    transform: rotate(90deg);
  }
}

.tu-c-loading-bar {
  margin: 0 auto;
  height: $ln-spacing-unit-x4;
  text-align: center;
}

.tu-c-loading-bar__item {
  background-color: $tu-color-resolution-blue;
  height: 100%;
  width: $tu-bar-width;
  display: inline-block;
  margin: 0 ($tu-bar-width / 2);
  animation: sk-stretchdelay 1.2s infinite ease-in-out;

  &--2 {
    animation-delay: -1.1s;
    background-color: $tu-color-cerulean;
  }

  &--3 {
    animation-delay: -1s;
  }

  &--4 {
    animation-delay: -0.9s;
    background-color: $tu-color-cerulean;
  }

  &--5 {
    animation-delay: -0.8s;
  }
}
