$tu-box-shadow: 0 0 15px 5px rgba(50, 50, 50, 0.2);
$table-border-color: $tu-color-quill-gray;
$min-width: 400px;

.tu-c-size-guide {
  @include ln-full-screen;
  right: -100%;
  overflow: auto;
  background-color: $ln-color-white;
  transition: right 0.5s ease;

  li {
    list-style: none;
  }

  &__close-button {
    background-color: #f6f6f6;
    border-radius: 100%;

    &:focus {
      outline: none;
    }
  }

  @include ln-respond-to('xs') {
    min-width: $min-width;
    width: 40%;
  }

  &.active {
    box-shadow: $tu-box-shadow;
    right: 0;
  }

  &__filter-button {
    width: 48%;

    &--disabled {
      border: 1px solid $table-border-color;
    }

    &:focus {
      outline: none;
    }
  }

  &__accordion {
    border-bottom: 1px solid $table-border-color;
  }

  &__table {
    table-layout: fixed;

    &--cell {
      border: 1px solid $table-border-color;
    }
  }
}

.tu-c-size-guide-link {
  width: ($ln-spacing-unit-x2 * 5);
  height: ($ln-spacing-unit * 3.5);
  margin-top: $ln-spacing-unit;
  border-radius: $ln-spacing-unit-x2;
  background-color: $ln-color-white;
  outline: none;

  @include ln-respond-to('xs') {
    width: ($ln-spacing-unit-x3 * 2);
    height: $ln-spacing-unit-x2;
  }

  span {
    color: $tu-color-black;
    text-decoration: underline;
  }
}
