.tu-video-container {
  position: relative;

  .tu-video__play-button {
    background-color: $tu-color-black;
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 53px;
    font-size: 40px;

    .ln-c-icon {
      padding-left: 3px;
    }
  }

  @include ln-respond-to('max-xs') {
    video {
      width: 100%;
    }

    .tu-video__play-button {
      width: 25px;
      height: 25px;
      line-height: 26px;
      font-size: 20px;
    }
  }
}
