// Colour names derived from Name That Colour: http://chir.ag/projects/name-that-color
$tu-color-nevada: #676c73;
$tu-color-oslo-gray: #808991;
$tu-color-silk: #bbafa9;
$tu-color-resolution-blue: #003087; //Paypal dark blue
$tu-color-cerulean: #009cde; // Paypal blue
$tu-color-wild-sand: #f6f6f6; // Background grey
$tu-color-quill-gray: #d9d9d6; //Light grey
$tu-color-boulder: #767676; // dark grey
$tu-color-jungle-green: #26b163; // Positive Green
$tu-color-jungle-green-dark: #3b7b4a; // Positive dark Green
$tu-color-regent-st-blue: #a2c7e6; // Interaction
$tu-color-tall-poppy: #c42626; // Error red
// Brand colours
$tu-color-violet: #771ae0; // Nectar
$tu-color-turbo: #f0dd00; // Season badges
$tu-color-tangerine-yellow: #ffc911; // Season badges
$tu-color-pomegranate: #e53212; // Sales
$tu-color-rose: #e50075; // Stunt
$tu-color-red: #f00; // Discount (double check)
$tu-color-cape-cod: #3d4042;
$tu-color-black: #000;
$tu-color-white: #fff;

$ln-color-error: $tu-color-tall-poppy;
$ln-color-black: $tu-color-black;
$ln-color-site-background: $ln-color-grey-v-light;
$ln-navigation-background-color: $ln-color-white;
$ln-color-button-primary-background: $tu-color-cape-cod;
$ln-color-button-secondary-hover: $tu-color-cape-cod;
$ln-color-button-primary-background-hover: $tu-color-black;
$ln-color-button-disabled: $ln-color-white;
$ln-color-button-primary-disabled-background: $tu-color-quill-gray;
$ln-color-links: $tu-color-cape-cod;
$ln-color-links--hover: $tu-color-black;

$tu-input-background: $tu-color-wild-sand;
$tu-input-interaction: $tu-color-regent-st-blue;
$tu-input-success: $tu-color-jungle-green;
$tu-input-error: $tu-color-tall-poppy;
$tu-input-warning: $tu-color-turbo;

$ln-overlay-background: rgba($ln-color-black, 0.6);
$ln-overlay-background--white: rgba($ln-color-white, 0.7);
