.ln-o-site-wrap {
  max-width: $ln-site-wrap-max-width;
  margin: 0 auto;
}

/*
Adds padding, required if there's to be objects or components such as the
grid system that contains negative margin equal to `$half-spacing-unit`.
*/
.ln-o-site-wrap--padding {
  max-width: ($ln-site-wrap-max-width + $ln-spacing-unit-double);
  padding-left: $ln-spacing-unit;
  padding-right: $ln-spacing-unit;
}

/* Squash */

.ln-o-site-wrap--squash {
  @include ln-respond-to('max-xl') {
    max-width: $ln-site-wrap-squeeze-max-width;
  }
}

.ln-o-site-wrap--padding.ln-o-site-wrap--squash {
  @include ln-respond-to('max-xl') {
    max-width: ($ln-site-wrap-squeeze-max-width + $ln-spacing-unit-double);
  }
}


/* Large */

.ln-o-site-wrap--large {
  max-width: $ln-site-wrap-large-max-width;
}

.ln-o-site-wrap--large.ln-o-site-wrap--padding {
  max-width: ($ln-site-wrap-large-max-width + $ln-spacing-unit-double);
}
