%paypal-heading-size {
  @each $key, $value in map-get($ln-font-sizes, 'h4') {
    @if $key == 'def' {
      font-size: $value;
    } @else {
      @include ln-respond-to($key) {
        font-size: $value;
      }
    }
  }
}

/* temp - remove when rebuilt */
.postCodeSearch,
.paymentFormPostcodeLookup {
  position: relative;
}

.ln-c-field-info {
  display: flex;
  align-items: center;
  padding-top: 0;
  height: 0;
  overflow: hidden;
  margin-bottom: 0;

  &::before {
    $size: 20px;
    display: none;
    font-weight: 700;
    font-size: $size;
    line-height: $size;
    height: 100%;
    text-align: center;
    min-height: $size;
    margin-right: 0;
    width: $size;
  }

  .has-error &,
  .has-warning & {
    height: auto;
    overflow: visible;
  }
  /* temp - remove when rebuilt */
  .postCodeSearch .has-error & {
    min-width: 275px;
    position: absolute;
    bottom: -30px;
  }
  /* temp - remove when rebuilt */
  .paymentFormPostcodeLookup .has-error & {
    min-width: 275px;
    position: absolute;
    bottom: -13px;
  }

  .ln-c-field-info--block & {
    background-image: linear-gradient(
      to right,
      $ln-color-error 30px,
      $tu-color-wild-sand 30px
    );
    background-color: $tu-color-wild-sand;
    position: relative;

    &::before {
      content: '!';
      $sizelarge: 40px;
      border-radius: 0;
      display: inline-block;
      min-width: 30px;
      line-height: $sizelarge;
      min-height: $sizelarge;
    }
  }

  .ln-c-field-info--white & {
    background-image: linear-gradient(
      to right,
      $ln-color-error 30px,
      $ln-color-white 30px
    );
    background-color: $ln-color-white;
  }
}

.ln-c-field-info--error {
  font-weight: normal;
  color: $ln-color-error;

  &::before {
    background: $ln-color-error;
    margin-right: 0;
  }
}

.ln-c-field-info--positive {
  color: $tu-color-black;

  .ln-c-field-info--block.has-error--positive & {
    background-image: linear-gradient(
      to right,
      $tu-input-interaction 30px,
      $tu-color-wild-sand 30px
    );
  }

  &::before {
    background: $tu-input-interaction;
    color: $ln-color-white;
  }
}

.ln-c-field-info--recaptcha {
  @include ln-respond-to('xs') {
    max-width: $tu-form-maxwidth;
  }

  &.has-error {
    margin-bottom: 1.5rem;
  }
}

.ln-c-field-info--success {
  color: $tu-color-black;

  .ln-c-field-info--block.has-error--success & {
    background-image: linear-gradient(
      to right,
      $tu-input-success 30px,
      $tu-color-wild-sand 30px
    );

    &::before {
      content: '';
    }

    &::after {
      content: '';
      @include ln-tick(8px, $ln-color-white, 11px, auto);
    }
  }

  &::before {
    background: $tu-input-success;
    color: $ln-color-white;
  }
}

.ln-c-field-info--warning {
  color: $tu-color-black;

  .ln-c-field-info--block.has-error--warning & {
    background-image: linear-gradient(
      to right,
      $tu-input-warning 30px,
      $tu-color-wild-sand 30px
    );
  }

  &::before {
    background: $tu-input-warning;
  }
}

.tu-c-field-info--paypal-error {
  border: 1px solid $ln-color-red;
  display: table;
  padding-top: 0;
  margin-bottom: 0;

  &::before {
    @extend %paypal-heading-size;
    border-radius: 0;
    display: table-cell;
    height: 100%;
    vertical-align: middle;
    width: ($ln-spacing-unit * 1.5);

    @include ln-respond-to('max-sm') {
      width: $ln-spacing-unit-x3;
    }
  }

  > div {
    display: table-cell;
    vertical-align: middle;
    padding-top: $ln-spacing-unit-half;
    padding-bottom: $ln-spacing-unit-half;
  }
}
