.tu-c-product__view-all {
  float: right;
}

.tu-c-product__title {
  float: left;
}

.tu-c-product-carousel {
  padding-left: $ln-spacing-unit;
  padding-right: $ln-spacing-unit;
  width: 100%;
  @include ln-respond-to('xs') {
    padding-left: $ln-spacing-unit-double;
    padding-right: 0;
  }
}

.tu-c-product-card {
  background-color: $ln-color-white;
  display: flex;
  flex-wrap: wrap;
  text-decoration: none;
  height: 100%;
  width: 100%;

  &:hover {
    text-decoration: underline;
  }

  img {
    width: 100%;
    max-width: 100%;
  }

  &--block {
    display: block;
  }

  &__tile-details {
    padding: $ln-spacing-unit-half;

    &__title {
      margin-bottom: $ln-spacing-unit-half;
    }
  }

  &__tile-price {
    color: $ln-color-grey-dark;
    margin-bottom: $ln-spacing-unit-half;

    &--sale {
      color: $ln-color-red;
    }
  }

  &__rating {
    margin-bottom: $ln-spacing-unit-half;
  }

  &__colours {
    color: $tu-color-cape-cod;
  }

  &__tile-details,
  &__tile-image {
    display: block;
    width: 100%;
  }

  &__navigation {
    display: flex;
    margin-top: $ln-spacing-unit;
    align-items: center;
    justify-content: center;
  }

  &__swatch {
    width: 1.875em;
    height: auto;
    display: block;
    line-height: 0;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid $tu-color-quill-gray;
    }

    &--active::before {
      border: 2px solid $ln-color-black;
      top: -1px;
      left: -1px;
      right: -1px;
      bottom: -1px;
    }
  }

  a:hover {
    text-decoration: none;
  }
}

// Nudge carousel dots up if there is a banner across the bottom of the image
.tu-c-productimagery-carousel.has-banner .imagePagination {
  bottom: 3rem;

  @media only screen and (min-width: 460px) {
    bottom: 2rem;
  }
}

.tu-c-product-card__images {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-bottom: 135%;
  position: relative;
}

.tu-c-product-card__image {
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
}

.tu-c-product-card__link {
  .tu-c-product-card__img {
    opacity: 0;
    position: absolute;
    z-index: $tu-z-index-base;

    &--primary {
      z-index: ($tu-z-index-base + 1);

      .tu-c-product-container--flat & {
        z-index: $tu-z-index-base;
      }
    }

    &--loaded {
      opacity: 1;
    }
  }

  &:hover {
    .tu-c-product-card__img--secondary {
      z-index: ($tu-z-index-base * 2);
    }

    .tu-c-product-container--flat & {
      .tu-c-product-card__img--primary {
        z-index: ($tu-z-index-base * 3);
      }
    }
  }
}

// Ensure unloaded images still have a height for the intersection observer
.tu-c-product-card__img:not(.tu-c-product-card__img--loaded) {
  min-height: 100%;
}

.tu-c-product-card__label {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: $ln-spacing-unit-half;
  text-align: center;
  z-index: (($tu-z-index-base * 3) + 1);
}

.tu-c-product__loadmore {
  min-height: 65px;
}

.tu-c-productimagery-carousel:empty::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  animation: loading-pulse 1s linear alternate infinite;
}

@keyframes loading-pulse {
  0% {
    background-color: $tu-color-white;
  }

  100% {
    background-color: $tu-color-quill-gray;
  }
}
