.tu-u-h1--secondary {
  font-weight: $ln-font-weight-medium;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.tu-u-font {
  &--bold {
    font-weight: 700;
  }

  &-line-through {
    text-decoration: line-through;
  }
}

.tu-text-uppercase,
.tu-u-text-uppercase {
  text-transform: uppercase;
  letter-spacing: 2px;
}

.ln-u-hero {
  letter-spacing: $ln-spacing-unit-half;
  text-transform: uppercase;
}

.tu-u-text-line-through {
  text-decoration: line-through;
}

.tu-u-heading--information {
  display: flex;
  align-items: center;

  &::before {
    display: inline-block;
    font-weight: $ln-font-weight-regular;
    margin-right: $ln-spacing-unit-half;
    @include ln-circle(1.5em);
  }
}
