@mixin ln-input-icon($direction, $icon) {
  padding-#{$direction}: ($ln-spacing-unit * 2.5); // magic number, ensures text doesn't flow too close to the icon
  background-position: $direction $ln-spacing-unit top 50%;
  background-size: auto 35%;
  background-repeat: no-repeat;
  background-image: url('data:image/svg+xml;base64,#{$icon}');
}
