.tu-t-header {
  background-color: $ln-color-white;
  border-bottom: 1px solid $tu-color-quill-gray;

  @include ln-respond-to('sm') {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.1);
    margin-bottom: $ln-spacing-unit;
    border-bottom-width: 0;
  }
}

.tu-t-header__logo {
  display: inline-block;
  padding: $ln-spacing-unit;

  @include ln-respond-to('sm') {
    padding: 1.5rem;
  }
}
