@if $use-ln-input-group {
  $input-border-width: 2px;

  .ln-c-input-group {
    position: relative;

    &.ln-c-input-group--has-button:hover::after {
      // Hide input hover shadow
      content: ' ';
      position: absolute;
      right: 0;
      width: 1px;
      background: $ln-color-white;
      top: $input-border-width;
      bottom: $input-border-width;
    }
  }

  .ln-c-input-group__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: $ln-spacing-unit;
    height: map-get($ln-icon-sizes, 'def');
    width: map-get($ln-icon-sizes, 'def');
    color: $ln-color-black;
  }

  .ln-c-input-group__action {
    position: absolute;
    top: $input-border-width;
    bottom: $input-border-width;
    right: $input-border-width;
    padding: 0 $ln-spacing-unit;
    color: $ln-color-black;
    background-color: $ln-color-white;
    min-height: auto;
    cursor: pointer;

    .ln-c-input-group--has-button & {
      right: 0;
    }
  }

  .ln-c-input-group__control {
    .ln-c-input-group--has-icon & {
      padding-left: calc(#{$ln-spacing-unit * 2} + #{map-get($ln-icon-sizes, 'def')});
    }

    .ln-c-input-group--has-action & {
      padding-right: calc(#{$ln-spacing-unit * 2} + #{map-get($ln-icon-sizes, 'def')});

      &::-ms-clear {
        display: none;
      }
    }

    .ln-c-input-group--has-button & {
      border-right: none;
    }
  }
}
