.tu-homepage__container {
  background-position: 50%;

  &--fullwidth {
    background-repeat: no-repeat;
    background-size: cover;
  }

  @include ln-respond-to('max-xs') {
    margin-left: $ln-spacing-unit;
    margin-right: $ln-spacing-unit;
  }
}

.tu-homepage__section {
  display: block;

  .tu-homepage__section {
    margin-left: 0;
  }

  .ln-u-popup-layer-wrapper {
    position: initial;
  }

  .ln-u-popup-inner-wrapper {
    position: inherit;
  }

  @include ln-respond-to('xs') {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    flex: 1 0 auto;
    margin-left: -$ln-spacing-unit-double;

    &--product {
      margin-left: $ln-spacing-unit-double;
    }
  }
}

.tu-c-homepage__item {
  display: block;
  width: 100%;

  &--carousel {
    padding-left: $ln-spacing-unit-double;
  }

  &--nopadding {
    margin-top: -$ln-spacing-unit;
    margin-bottom: -$ln-spacing-unit;
  }

  img {
    display: block;
    max-width: 100%;
    width: 100%;
  }

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    &--doubleweight {
      width: 150%;
    }
  }

  @include ln-respond-to('xs') {
    display: flex;
    justify-content: flex-start;
    flex: 2 1 0%;
    padding-left: $ln-spacing-unit-double;

    &--linkgroup {
      display: block;
    }

    * {
      max-width: 100%;
    }

    &--row {
      flex-direction: row;
    }

    &--singlepanel {
      flex-wrap: wrap;
    }

    &--doubleweight {
      flex-grow: 3;
    }

    &--reverse {
      order: 2;
    }
  }
}

.tu-c-homepage__item ~ .tu-c-homepage__item {
  @include ln-respond-to('max-xs') {
    margin-top: $ln-spacing-unit-double;
  }
}

.tu-c-homepage__blogcontent {
  display: block;
  background-color: $ln-color-white;
}

.tu-c-homepage__cell {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;

  @include ln-respond-to('xs') {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
  }

  &--spaced {
    background: transparent;
    justify-content: space-between;
  }

  // These are currently here because Max-nesting-limit reached in linter
  &--multi-padding {
    .tu-c-homepage__cell:nth-child(1) {
      padding-right: $ln-spacing-unit-half;
    }

    .tu-c-homepage__cell:nth-child(2) {
      padding-left: $ln-spacing-unit-half;
    }
  }

  &--multi-items {
    overflow: hidden;
    flex-wrap: nowrap;
    flex-direction: row;
  }

  &--left {
    align-items: flex-start;

    .tu-c-homepage__textcontent {
      justify-content: flex-start;
    }
  }

  &--right {
    align-items: flex-end;

    .tu-c-homepage__textcontent {
      justify-content: flex-end;
    }
  }

  &--top {
    justify-content: flex-start;

    .tu-c-homepage__textcontent {
      align-items: flex-start;
    }
  }

  &--bottom {
    justify-content: flex-end;

    .tu-c-homepage__textcontent {
      align-items: flex-end;
    }
  }
}

.tu-c-homepage__imagecontent {
  display: flex;
  align-items: center;
  background-color: $ln-color-white;
  overflow: hidden;
  text-decoration: none;
  width: 100%;

  &--column {
    flex-direction: column;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      justify-content: space-between;
    }
  }
}

.tu-c-homepage__textcontent {
  padding: $ln-spacing-unit;

  &--image-background {
    display: block;
    position: relative;
    max-width: 100%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    align-items: center;
    justify-content: center;

    @include ln-respond-to('xs') {
      display: flex;
      position: absolute;
    }
  }
}

.tu-c-homepage__image {
  width: 55%;
  overflow: hidden;

  img {
    display: block;
  }

  &--reverse {
    order: 2;
  }

  .tu-c-homepage__imagecontent--column & {
    width: 100%;
  }
}

.tu-c-homepage__content {
  width: 45%;

  .tu-c-homepage__imagecontent--column & {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    flex: 1;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      flex: none;
    }
  }
}

.tu-c-homepage__links {
  max-width: none;

  &--multi {
    width: 100%;

    li {
      min-width: 49%;
    }
  }

  a,
  a:visited,
  a:hover,
  a:active {
    background-color: $ln-color-transparent;
    background-image: none;
    min-height: $ln-size-def;
    min-width: $ln-size-def;
    padding: $ln-spacing-unit;
    text-align: center;
    text-decoration: none;
    align-items: center;
    display: inline-flex;
    justify-content: center;
    text-transform: uppercase;
    font-weight: 900;
    border: 2px solid $ln-color-black;
    color: $tu-color-black;
    letter-spacing: 1px;
  }

  &--white {
    a,
    a:visited,
    a:hover,
    a:active {
      border: 2px solid $ln-color-white;
      color: $ln-color-white;
    }
  }
}
