%fixed-block {
  position: fixed;
  animation-name: fadeIn;
  animation-duration: 1s;
  margin: 0 auto;
  left: 10px;
  right: 10px;

  .mm-page & {
    left: 0;
    right: 0;
  }
}

.tu-c-sub-total {
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.1);
  max-width: $tu-max-width;
  z-index: 5;
}

.tu-c-sub-total__main {
  justify-content: space-between;
}

.tu-c-sub-total__display-show {
  display: flex;
  @include ln-respond-to('max-sm') {
    display: block;
  }
}

.tu-c-sub-total__item-wrapper {
  @include ln-respond-to('max-sm') {
    padding: 1.25rem $ln-spacing-unit;
    width: 100%;
  }
}

.tu-c-sub-total__item-block-wrapper {
  padding: 1.25rem $ln-spacing-unit;
  @include ln-respond-to('max-sm') {
    flex-grow: 0;
    display: flex;
    margin-right: 0;
    padding: 0;
  }
}

.tu-c-sub-total__item-wrapper-outer {
  width: auto;
  min-width: 25rem;
}

.tu-c-sub-total__item-sub-total {
  min-width: 5rem;
  border-left: 1px solid $tu-color-quill-gray;
  @include ln-respond-to('max-sm') {
    width: auto;
    border-top: 1px solid $tu-color-quill-gray;
    border-left: none;
  }
}

.tu-c-sub-total__items-total {
  width: auto;
  padding: 1.25rem $ln-spacing-unit;
  @include ln-respond-to('max-sm') {
    display: flex;
    width: 100%;
    padding: 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
}

.tu-c-sub-total__bottom-offset-checkout {
  @extend %fixed-block;
  bottom: 0;
}

.tu-c-sub-total__item-basket {
  @include ln-respond-to('max-sm') {
    display: flex;
  }
}

.tu-c-sub-total__item-block {
  padding-right: 0.5rem;
  @include ln-respond-to('max-sm') {
    flex-grow: 1;
  }
}

.tu-c-sub-total__items-total-inner {
  line-height: 1.5;
}

.tu-c-sub-total__top-offset-checkout {
  @extend %fixed-block;
  top: 0;
  bottom: auto;
}
