.ln-c-standalone-link {
  @include ln-font-size('h6');
  font-weight: $ln-font-weight-bold;
  color: $ln-color-links;
  border-color: $ln-color-links;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $ln-color-links--hover;
    border-color: $ln-color-links--hover;
  }
}

.ln-c-text-link-alt {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: $ln-font-weight-medium;
  color: $tu-color-black;
  background: url('data:image/svg+xml;base64,')
    no-repeat right center;
  padding: 0 35px 0 0;
}
