.tu-c-checkout-order-summary {
  $qty-dim: 1.75rem;

  &-container {
    max-width: 25rem;
    width: 100%;

    @include ln-respond-to('sm') {
      max-width: 23rem;
    }

    .order-summary {
      color: $tu-color-black;
    }
  }

  &-root {
    display: flex;
    justify-content: center;
    padding: 2rem 0.75rem;
  }

  &__product-tiles {
    display: flex;
    margin-top: 1rem;
  }

  &__image {
    margin-right: 0.75rem;
    position: relative;
    width: 4rem;

    &__qty {
      display: flex;
      justify-content: center;
      align-items: center;
      height: $qty-dim;
      min-width: $qty-dim;
      color: $tu-color-white;
      text-align: center;
      border-radius: 1rem;
      background-color: $tu-color-black;
      position: absolute;
      right: -5px;
      bottom: -5px;
    }
  }

  &-product-items-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: $ln-spacing-unit * 2;
    border-bottom: 1px solid $tu-color-quill-gray;
    animation: none;

    &:empty {
      border-bottom: none;
    }

    .view-cart {
      font-family: $ln-font-family-brand;
      font-weight: bold;
      color: $tu-color-black;
      background-color: $tu-color-white;
      text-decoration: underline;
    }
  }

  &-fulfilment-details {
    color: $tu-color-black;
    animation: none;

    &-container {
      padding: $ln-spacing-unit * 2 0;
      border-bottom: 1px solid $tu-color-quill-gray;
    }

    &-container:empty {
      border-bottom: none;
    }

    .header-container {
      display: flex;
      justify-content: space-between;
    }

    .type-header {
      font-weight: 900;
    }

    .change-link {
      font-weight: bold;
      color: $tu-color-black;
    }

    .method-header {
      font-weight: 900;
      margin-top: $ln-spacing-unit;
    }
  }

  &__nectar {
    display: block;
    animation: none;
    padding-bottom: $ln-spacing-unit * 2;
    border-bottom: 1px solid $tu-color-quill-gray;

    &--active {
      transform: rotate(180deg);
    }

    &__add {
      margin-top: $ln-spacing-unit;
    }

    &__logo {
      width: 35px;
      margin: 0;
    }

    &__points-container {
      display: flex;
      align-items: center;
    }

    &__points-info {
      margin: 0 10px;
    }

    &__points-hl {
      color: $tu-color-violet;
      font-weight: bold;
    }

    &__add-link {
      text-decoration: underline;
      font-weight: bold;
    }
  }

  &__nectar__card {
    padding-top: $ln-spacing-unit;

    @include ln-respond-to('sm') {
      padding: $ln-spacing-unit $ln-spacing-unit 0;
    }
  }

  &__nectar__details {
    padding-top: $ln-spacing-unit;

    @include ln-respond-to('sm') {
      padding: $ln-spacing-unit $ln-spacing-unit * 2 0;
    }
  }

  &__nectar__form {
    display: flex;
    flex-direction: column;
    margin-top: $ln-spacing-unit;

    .input-container {
      width: 60%;
    }

    .input {
      display: flex;
      justify-content: space-between;
    }

    .error {
      margin-top: $ln-spacing-unit * 0.25;
    }

    .button,
    .button--loading {
      color: $tu-color-black;
      background-color: $tu-color-white;
      border: 2px solid $tu-color-black;
    }

    .button--loading circle {
      stroke: $tu-color-black;
    }
  }

  &__nectar__points {
    display: flex;
    border: solid 2px $tu-color-black;
    padding: $ln-spacing-unit;
    margin-top: $ln-spacing-unit * 1.5;

    .logo {
      width: 35px;
      height: 30px;
    }

    .details {
      display: flex;
      flex-direction: column;
      flex: 1;
      margin: $ln-spacing-unit-half $ln-spacing-unit 0;
    }

    .details--bold {
      color: $tu-color-violet;
      font-weight: 900;
    }

    .select-icon {
      width: 35px;
      height: 35px;
    }
  }

  &__nectar__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: transparent;
    font-family: $ln-font-family-brand;

    @include ln-respond-to('sm') {
      font-size: 0.8rem;
    }
  }

  &-cart-details {
    display: flex;
    flex-direction: column;
    padding: $ln-spacing-unit * 1.5 $ln-spacing-unit * 1.5 0;
    color: $ln-color-black;
    background-color: $ln-color-white;
    height: 100vh;
    width: 100vw;

    .product-details-container {
      overflow: auto;
    }

    @include ln-respond-to('sm') {
      height: auto;
      max-height: 90vh;
      max-width: 23rem;
    }
  }

  &-product-details {
    display: flex;
    margin-bottom: $ln-spacing-unit;
    color: $tu-color-black;

    .attributes {
      display: flex;
      flex-direction: column;
      flex: 4;
    }

    .product-header {
      display: flex;
      justify-content: space-between;
    }

    .name {
      font-weight: 900;
      margin-bottom: $ln-spacing-unit;
    }

    .was-price,
    .was-was-price {
      text-decoration: line-through;
    }

    .total-price {
      font-weight: normal;
      margin-right: $ln-spacing-unit;
    }

    .image {
      margin-right: $ln-spacing-unit;
      flex: 1;
    }

    .attribute-label {
      font-weight: bold;
    }

    .attribute-value {
      margin-left: $ln-spacing-unit-half;
    }

    .discounts {
      color: $tu-color-tall-poppy;
    }
  }

  &-order-price-summary {
    padding: 2 * $ln-spacing-unit 0;
    border-bottom: 1px solid $tu-color-quill-gray;

    &:empty {
      border-bottom: none;
    }

    .row {
      display: flex;
      padding: 0.25 * $ln-spacing-unit 0;
      justify-content: space-between;
    }

    .total {
      font-weight: 900;
    }

    .discount {
      color: $tu-color-tall-poppy;
      font-weight: bold;
    }
  }

  &-proceed-to-payment__form {
    display: flex;
    justify-content: center;
    margin-top: $ln-spacing-unit * 3.5;

    @include ln-respond-to('sm') {
      margin-top: $ln-spacing-unit * 2;
    }
  }

  &-proceed-to-payment__button {
    text-align: center;
  }
}

.tu-c-checkout-order-summary--loading {
  $loader-third: 33.3%;
  $loader-half: 50%;

  .tu-c-checkout-order-summary-items:empty {
    $title: 100% 30px, 0 0;
    $summary: $loader-half 20px, 0 45px;
    $loader-blocks: $title, $summary;
    @include background-image-blocks($loader-blocks);
    height: 70px;
  }

  .tu-c-checkout-order-summary-product-items-container:empty {
    $first-product-tile: 48px 48px, 0 20px;
    $second-product-tile: 48px 48px, 58px 20px;
    $third-product-tile: 48px 48px, 116px 20px;
    $view-cart-details-link: 110px 20px, 100% 48px;
    $loader-blocks: $first-product-tile, $second-product-tile,
      $third-product-tile, $view-cart-details-link;
    @include background-image-blocks($loader-blocks);
    height: 100px;
    border-bottom: 1px solid $tu-color-quill-gray;

    @include ln-respond-to('sm') {
      $first-product-tile: 80px 80px, 0 20px;
      $second-product-tile: 80px 80px, 100px 20px;
      $third-product-tile: 80px 80px, 200px 20px;
      $view-cart-details: 110px 20px, 100% 80px;
      $loader-blocks: $first-product-tile, $second-product-tile,
        $third-product-tile, $view-cart-details;
      @include background-image-blocks($loader-blocks);
      height: 130px;
    }
  }

  .tu-c-checkout-order-summary-fulfilment-details-container:empty {
    $collect-from: $loader-half 20px, 0 30px;
    $collect-from-paragraph: $loader-half 60px, 0 60px;
    $collect-method: $loader-half 20px, 0 140px;
    $collect-method-paragraph: $loader-half 20px, 0 170px;
    $change-link: 50px 20px, 100% 30px;
    $loader-blocks: $collect-from, $collect-from-paragraph, $collect-method,
      $collect-method-paragraph, $change-link;
    @include background-image-blocks($loader-blocks);
    height: 220px;
    border-bottom: 1px solid $tu-color-quill-gray;
  }

  .tu-c-checkout-order-summary-order-price-summary:empty {
    $subtotal-label: $loader-third 20px, 0 30px;
    $fulfilment-label: $loader-third 20px, 0 65px;
    $discount-label: $loader-third 20px, 0 95px;
    $total-price-label: $loader-third 20px, 0 130px;
    $subtotal-value: 60px 20px, 100% 30px;
    $fulfilment-value: 60px 20px, 100% 65px;
    $discount-value: 60px 20px, 100% 95px;
    $total-price-value: 60px 20px, 100% 130px;
    $loader-blocks: $subtotal-label, $fulfilment-label, $discount-label,
      $total-price-label, $subtotal-value, $fulfilment-value, $discount-value,
      $total-price-value;
    @include background-image-blocks($loader-blocks);
    height: 180px;
    border-bottom: 1px solid $tu-color-quill-gray;
  }

  .tu-c-checkout-order-summary__nectar:empty {
    $nectar-points: $loader-half 30px, 0 20px;
    $add-link: 60px 30px, 100% 20px;
    $loader-blocks: $nectar-points, $add-link;
    @include background-image-blocks($loader-blocks);
    height: 100px;
  }

  .tu-c-checkout-order-summary-proceed-to-payment__form:empty {
    $payment-button: 65% 50%, 0 50%;
    @include background-image-blocks($payment-button);
    height: 50px;
  }
}

.tu-c-checkout-order-summary-cart-details-overlay {
  .overlay-container {
    background: rgba(0, 0, 0, 0.25);
    z-index: 10000;
  }

  .overlay-content {
    display: flex;
  }

  .close-button-container {
    margin-left: 0;
  }

  .close-button-container svg {
    background-color: transparent;
    fill: $tu-color-black;
    margin-top: 1.5 * $ln-spacing-unit;
    margin-right: 0.75 * $ln-spacing-unit;
    margin-bottom: 0;
    margin-left: -3 * $ln-spacing-unit;
    width: 3 * $ln-spacing-unit;
    height: 3 * $ln-spacing-unit;

    @include ln-respond-to('sm') {
      width: 2 * $ln-spacing-unit;
      height: 2 * $ln-spacing-unit;
    }
  }
}
