.tu-t-footer__links {
  background-color: $tu-color-black;
  padding-top: 1.5rem;

  * {
    color: $ln-color-white !important;
  }

  h3 {
    margin-bottom: 0.25rem;
  }

  li a {
    line-height: 1.5rem;
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }

  @include ln-respond-to('xs') {
    padding-top: 3rem;
  }

  .is-open > .ln-c-foldable__body {
    padding-top: 0.5rem;
  }

  .ln-c-foldable__body .yCmsComponent {
    margin-top: 0.25rem;
  }
}

.tu-t-footer__social {
  border: 1px solid $tu-color-boulder;
  border-width: 1px 0 0;

  @include ln-respond-to('sm') {
    border-right-width: 1px;
  }
}

.tu-t-footer__cards {
  border: 1px solid $tu-color-boulder;
  border-width: 1px 0 0;
}

.tu-t-footer__notices {
  background-color: $tu-color-boulder;
}

.tu-t-footer__notices-link {
  border: 1px solid $tu-color-quill-gray;
  border-width: 0 0 1px;
  text-align: center;

  &:nth-child(odd) {
    border-right-width: 1px;
  }

  @include ln-respond-to('sm') {
    border: 0;
  }
}

.tu-t-footer__copyright {
  text-align: center;

  @include ln-respond-to('sm') {
    text-align: right;
  }
}
