@mixin ln-c-foldable-build-segments {
  > .ln-c-foldable__head {
    padding-top: $ln-spacing-unit;
    padding-bottom: $ln-spacing-unit;
  }

  > .ln-c-foldable__head > .ln-c-foldable__title {
    margin-bottom: 0;
    padding-right: $ln-spacing-unit-double;
  }

  > .ln-c-foldable__head > .ln-c-foldable__toggle {
    display: block;
  }

  > .ln-c-foldable__body {
    height: 0;
    overflow: hidden;
  }
}

@if $use-ln-foldable {
  .ln-c-foldable--bordered:first-child {
    border-top: 1px solid $ln-color-base-border;
  }

  .ln-c-foldable--bordered {
    border-bottom: 1px solid $ln-color-base-border;
  }

  .ln-c-foldable--bordered .ln-c-foldable__head {
    padding-top: $ln-spacing-unit;
  }

  .ln-c-foldable__head {
    position: relative;
  }

  .ln-c-foldable__toggle {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    width: 100%;
    color: currentColor;
    background: transparent;
  }

  .ln-c-foldable__chevron {
    position: absolute;
    right: 0;
    top: 50%;
    transform: rotate(0) translateY(-50%);
  }

  .ln-c-foldable:not([class*='ln-c-foldable@']) {
    @include ln-c-foldable-build-segments;
  }

  @each $key, $value in $ln-breakpoints {
    .ln-c-foldable\@max-#{$key} {
      @include ln-respond-to('max-#{$key}') {
        @include ln-c-foldable-build-segments;
      }
    }
  }

  [class*='ln-c-foldable'].is-open > .ln-c-foldable__body {
    height: auto;
    overflow: visible;
  }

  // reason: Requires the high-specificity selector below to allow nesting of foldable components.
  // stylelint-disable-next-line
  [class*='ln-c-foldable'].is-open > .ln-c-foldable__head > .ln-c-foldable__toggle > .ln-c-foldable__chevron {
    transform: rotate(180deg) translateY(50%);
  }
}
