$icon-size: 20px;
$radio-dot-size: 14px;
$label-color: $tu-color-black;
$label-disabled-color: $tu-color-cape-cod;
$highlight-color: $tu-color-black;
$highlight-disabled-color: $tu-color-quill-gray;

.ln-c-form-option__label {
  color: $label-color;
  padding-left: calc(#{$icon-size} + #{$ln-spacing-unit});
  padding-right: 0;

  &::before {
    left: 0;
  }

  .ln-c-form-option--radio & {
    @include ln-respond-to('xs') {
      padding-left: calc(#{$icon-size} + #{$ln-spacing-unit-half});
    }
  }

  .ln-c-form-option--radio &::after {
    left: calc(#{($ln-spacing-unit-half / 2)} + 2px);

    @include ln-respond-to('xs') {
      left: calc(#{($ln-spacing-unit-half / 2)});
    }
  }
}

.ln-c-form-option__input[disabled] ~ .ln-c-form-option__label {
  color: $label-disabled-color;

  &::before {
    border-color: $highlight-disabled-color;
  }
}

.ln-c-form-option__input:not([disabled]) ~ .ln-c-form-option__label {
  &:hover,
  &:focus {
    background-color: transparent;
  }
}

.ln-c-form-option__input:checked {
  .ln-c-form-option--checkbox & ~ .ln-c-form-option__label {
    &::before {
      border-color: $highlight-color;
      background-color: $highlight-color;
    }
  }

  .ln-c-form-option--radio & ~ .ln-c-form-option__label {
    &::before {
      border-color: $tu-color-cape-cod;
    }

    &::after {
      background-color: $tu-color-cape-cod;
    }
  }

  .customer-options--single-cta
    .ln-c-form-option--radio
    &
    ~ .ln-c-form-option__label {
    &::before {
      border-color: transparent;
    }

    &::after {
      background-color: transparent;
    }
  }
}

.ln-c-form-option--facet {
  min-height: 0;
}

.tu-u-form-link {
  max-width: 220px;

  @include ln-respond-to('xs') {
    max-width: none;
  }
}
