$slide-sizes: (
  'state__height': 40px,
  'state__width': 80px,
  'state__before': 32px,
);

$alt-slide-sizes: (
  'state__height': 20px,
  'state__width': 40px,
  'state__before': 16px,
);

.ln-c-form-slider,
.ln-c-form-slider--alt {
  display: table;
  width: 100%;
}

.ln-c-form-slider__confirmed {
  display: block;
  overflow: hidden;
  width: 100%;
  transition: max-height 0.7s;
  max-height: 0;

  &--show {
    max-height: 200px;
  }
}

.ln-c-form-slider__title {
  display: table-cell;
  vertical-align: middle;
}

.ln-c-form-slider__mechanism {
  display: table;
  float: right;

  .ln-c-form-slider--alt & {
    display: flex;
    justify-content: center;
    float: none;
  }

  .ln-c-form-slider--sizeguide & {
    display: flex;
    justify-content: center;
  }

  &::before {
    content: 'no';
    display: table-cell;
    padding-right: 8px;
    text-transform: uppercase;
    vertical-align: middle;
    font-weight: 700;

    .ln-c-form-slider--alt & {
      content: 'Model';
      display: inline-block;
    }

    .ln-c-form-slider--sizeguide & {
      content: 'CM';
      display: inline-block;
    }
  }

  &::after {
    content: 'yes';
    display: table-cell;
    padding-left: 8px;
    text-transform: uppercase;
    vertical-align: middle;
    opacity: 0.7;
    font-weight: 700;

    .ln-c-form-slider--alt & {
      content: 'Flat shot';
      display: inline-block;
    }

    .ln-c-form-slider--sizeguide & {
      content: 'INCH';
      display: inline-block;
    }
  }
}

.ln-c-form-slider__state {
  background-color: $tu-color-wild-sand;
  border-radius: map-get($slide-sizes, 'state__height');
  display: table-cell;
  position: relative;
  width: map-get($slide-sizes, 'state__width');
  height: map-get($slide-sizes, 'state__height');

  .ln-c-form-slider--alt &,
  .ln-c-form-slider--sizeguide & {
    background-color: $tu-color-black;
    width: map-get($alt-slide-sizes, 'state__width');
    height: map-get($alt-slide-sizes, 'state__height');
    display: inline-block;
  }

  &::before {
    background-color: $tu-color-black;
    color: $ln-color-white;
    border-radius: 50%;
    content: 'X';
    display: inline-block;
    font-weight: 700;
    left: 4px;
    bottom: 4px;
    width: map-get($slide-sizes, 'state__before');
    height: map-get($slide-sizes, 'state__before');
    line-height: map-get($slide-sizes, 'state__before');
    position: absolute;
    text-align: center;
    transition: transform 0.4s;

    .ln-c-form-slider--alt &,
    .ln-c-form-slider--sizeguide & {
      background-color: $ln-color-white;
      content: '';
      left: 2px;
      bottom: 2px;
      width: map-get($alt-slide-sizes, 'state__before');
      height: map-get($alt-slide-sizes, 'state__before');
      line-height: map-get($alt-slide-sizes, 'state__before');
    }
  }
}

input:checked + .ln-c-form-slider__mechanism {
  &::before {
    opacity: 0.7;
  }

  &::after {
    opacity: 1;
  }

  > .ln-c-form-slider__state {
    background-color: $tu-color-jungle-green;

    .ln-c-form-slider--alt &,
    .ln-c-form-slider--sizeguide & {
      background-color: $tu-color-black;
    }

    &::before {
      background-color: $ln-color-white;
      color: $tu-color-black;
      transform: translateX(map-get($slide-sizes, 'state__height'));
      padding: 5px;
      content: url('data:image/svg+xml;base64,');

      .ln-c-form-slider--alt &,
      .ln-c-form-slider--sizeguide & {
        transform: translateX(map-get($alt-slide-sizes, 'state__height'));
        content: '';
      }
    }
  }
}
