.ln-c-select {
  font-family: inherit;
  font-weight: $ln-font-weight-medium;
  color: $tu-color-black;
  text-transform: uppercase;
  max-width: none;
  padding-top: 0;
  line-height: 2.85em;

  @include ln-respond-to('xs') {
    max-width: $tu-form-maxwidth;
  }

  &:not([disabled]) {
    .ln-c-form-group.has-error & {
      border: 1px solid $ln-color-error;
      border-left-width: 1px;
      color: $ln-color-error;
    }
  }

  &--alt {
    border: 1px solid $tu-color-quill-gray;
    border-width: 0 0 0 1px;
    background-color: transparent;
    background-size: auto 90%;
    line-height: 1.25em;
    min-height: 0;
    min-width: 0;
    max-width: 180px;
    margin-left: $ln-spacing-unit-half;
    padding-left: $ln-spacing-unit-half;
    padding-right: $ln-spacing-unit;
    background-position: right 0 top 50%;

    &:not([disabled]):hover {
      box-shadow: none;
      cursor: pointer;
    }
  }
}
